<template>
  <v-dialog
    v-model="dialog"
    width="700"
  >
    <v-card>
      <v-card-title>
        <span class="text-h6 text-uppercase">Edit Application Information</span>
        <v-spacer></v-spacer>
        <v-btn
          icon
          @click="closeModal"
        >
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-card-title>
      <v-divider></v-divider>
      <v-card-text class="pt-3 pb-6">
        <v-alert
          :value="!!editApplicationInfoError"
          dense
          outlined
          type="error"
          transition="scale-transition"
          icon="mdi-exclamation-thick"
          class="mb-3"
        >
          {{ editApplicationInfoError }}
        </v-alert>
        <v-form
          ref="form"
          v-model="valid"
          @submit.prevent="handleSubmit"
        >
          <div class="mb-2">
            <small>* indicates required field</small>
          </div>
          <v-row>
            <v-col cols="12">
              <v-autocomplete
                ref="sales_group"
                :rules="[rules.required]"
                v-model="sales_group"
                :items="salesGroups"
                label="Sales Group*"
                hide-details="auto"
              ></v-autocomplete>
            </v-col>
            <v-col cols="12">
              <!-- <p>{{ position }}</p> -->
              <v-autocomplete
                ref="position"
                :rules="[rules.required]"
                v-model="position"
                :items="positions"
                label="Position*"
                hide-details="auto"
                return-object
              ></v-autocomplete>
            </v-col>
            <v-col cols="12">
              <v-menu
                v-model="date_of_application_menu"
                :close-on-content-click="false"
                transition="scale-transition"
                offset-y
                min-width="auto"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    ref="date_of_application"
                    v-model="date_of_application"
                    label="Date of Application"
                    readonly
                    v-bind="attrs"
                    v-on="on"
                    @keyup="date_of_application_menu=true"
                    @keydown="date_of_application_menu=false"
                    hide-details="auto"
                    clearable
                  ></v-text-field>
                </template>
                <v-date-picker
                  v-model="date_of_application"
                  @input="date_of_application_menu = false"
                ></v-date-picker>
              </v-menu>
            </v-col>
          </v-row>
          <div class="mt-5">
            <v-btn
              :disabled="!valid"
              type="submit"
              color="blue darken-3 white--text"
              class="mr-2 mt-2"
              :loading="editApplicationInfoLoading"
              depressed
            >
              Save
            </v-btn>
            <v-btn
              @click="closeModal"
              color="secondary"
              class="mt-2"
              depressed
              outlined
            >
              Cancel
            </v-btn>
          </div>
        </v-form>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
import { mapState, mapActions } from "vuex"
import { isObjectEmpty } from '@/helpers/form'
// import salesGroups from '@/data/salesGroups.json'
import positions from '@/data/positions.json'
import formRules from '@/helpers/formRules'

export default {
  props: {
    modalData: {
      type: Object,
      required: true
    }
  },
  data: () => ({
    valid: true,
    rules: formRules,
    sales_group: '',
    // sales_groups: salesGroups,
    position: '',
    positions: positions,
    date_of_application: '',
    date_of_application_menu: false,
  }),
  computed: {
    ...mapState("applicants", ["editApplicationInfoLoading", "editApplicationInfoError", "editApplicationInfoErrors", "salesGroups"]),
    dialog: {
      get: function () {
        return this.modalData.dialog
      },
      // setter
      set: function () {
        this.closeModal()
      }
    },
  },
  mounted() {
    this.$refs.sales_group.focus()
    this.sales_group = parseInt(this.modalData.sales_group_id)
    this.position = this.getCurrentPosition(this.modalData.position_id)
    this.date_of_application = this.modalData.date_of_application
  },
  methods: {
    ...mapActions("applicants", ["editApplicationInfo"]),
    getCurrentPosition(id) {
      return this.positions.find((position) => position.value == parseInt(id))
    },
    getSalesGroupText(id) {
      return this.salesGroups.find((sg) => sg.value == parseInt(id)).text
    },
    async handleSubmit () {
      if (!this.editApplicationInfoLoading) {
        const editData = {
          applicant_id: this.modalData.id, 
          sales_group: this.sales_group, 
          position: this.position.value, 
          date_of_application: this.date_of_application
        }

        // console.log(editData)

        const res = await this.editApplicationInfo(editData)

        if (!this.editApplicationInfoError && isObjectEmpty(this.editApplicationInfoErrors)) {
          if (res.data.sales_group_id) {
            res.data.sales_group = this.getSalesGroupText(res.data.sales_group_id)
          }

          if (res.data.position_id) {
            res.data.position = this.getCurrentPosition(res.data.position_id).text
          }
          
          this.$emit('editedApplicationInfo', res)

          this.closeModal()
        }
      }
    },
    closeModal() { 
      this.$emit('closeModal')
    }
  },
}
</script>