<template>
  <v-dialog
    v-model="dialog"
    width="500px"
  >
    <v-card>
      <v-card-title>
        <span class="text-h6">Change Password</span>
      </v-card-title>
      <v-divider></v-divider>
      <v-card-text class="pt-3 pb-6">
        <v-alert
          :value="!!changePasswordError"
          dense
          outlined
          type="error"
          transition="scale-transition"
          icon="mdi-exclamation-thick"
          class="mb-3"
        >
          {{ changePasswordError }}
        </v-alert>
        <v-form
          ref="form"
          v-model="valid"
          @submit.prevent="handleSubmit"
        >
          <div class="mb-2">
            <small>You will be logged out after changing your password</small>
          </div>
          <v-row>
            <v-col
              cols="12"
            >
              <v-text-field
                ref="current_password"
                :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                :rules="[rules.required, wrongPassword]"
                :type="show1 ? 'text' : 'password'"
                label="Current Password"
                v-model="current_password"
                counter
                @click:append="show1 = !show1"
              ></v-text-field>
            </v-col>
            <v-col
              cols="12"
            >
              <v-text-field
                :append-icon="show2 ? 'mdi-eye' : 'mdi-eye-off'"
                :rules="[rules.required, rules.min, rules.strong_password]"
                :type="show2 ? 'text' : 'password'"
                label="New Password"
                hint="At least 8 characters"
                v-model="new_password"
                counter="50"
                @click:append="show2 = !show2"
              ></v-text-field>
            </v-col>
            <v-col
              cols="12"
            >
              <v-text-field
                :append-icon="show3 ? 'mdi-eye' : 'mdi-eye-off'"
                :rules="[rules.required, passwordConfirmationRule]"
                :type="show3 ? 'text' : 'password'"
                label="Confirm Password"
                hint="At least 8 characters"
                v-model="confirm_password"
                counter
                @click:append="show3 = !show3"
              ></v-text-field>
            </v-col>
          </v-row>
          <div class="mt-3">
            <v-btn
              :disabled="!valid"
              type="submit"
              color="blue darken-3 white--text"
              class="mr-2 mt-2"
              :loading="changePasswordLoading"
              depressed
            >
              Change
            </v-btn>
            <v-btn
              @click="closeModal"
              color="secondary"
              class="mt-2"
              depressed
              outlined
            >
              Cancel
            </v-btn>
          </div>
        </v-form>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
import { mapState, mapActions } from "vuex"
import { isObjectEmpty } from '@/helpers/form'

export default {
  props: {
    modalData: {
      type: Object,
      required: true
    }
  },
  data: () => ({
    valid: true,
    show1: false,
    show2: false,
    show3: false,
    rules: {
      required: v => !!v || 'This field is required.',
      strong_password: v => /^(?=.*?[A-Z])(?=.*?[0-9])(?=.*?[a-z])/.test(v) || 'Must contains at least one upper case, one lower case and one number.',
      min: v => v.length >= 8 || 'Min 8 characters',
    },
    current_password: '',
    new_password: '',
    confirm_password: '',
    wrongPasswordText: ''
  }),
  computed: {
    ...mapState("account", ["changePasswordLoading", "changePasswordError", "changePasswordErrors"]),
    dialog: {
      get: function () {
        return this.modalData.dialog
      },
      // setter
      set: function () {
        this.closeModal()
      }
    },
    passwordConfirmationRule() {
      return () => (this.new_password === this.confirm_password) || 'Password must match'
    }
  },
  mounted() {
    this.$refs.current_password.focus()
  },
  beforeDestroy() {
    this.$store.commit("account/setChangePasswordError", null)
  },
  methods: {
    ...mapActions("account", ["changePassword"]),
    wrongPassword(val) {
      if (val == this.wrongPasswordText) {
        return 'Wrong password'
      } else {
        return true
      }
    },
    async handleSubmit() {
      if (!this.changePasswordLoading) {
        await this.changePassword({ current_password: this.current_password, new_password: this.new_password, confirm_password: this.confirm_password })

        if (!this.changePasswordError && isObjectEmpty(this.changePasswordErrors)) {
          // reset form
          this.$emit('success')

          // this.closeModal()
        } else {
          if (!isObjectEmpty(this.changePasswordErrors)) {
            this.wrongPasswordText = this.current_password
            this.$refs['current_password'].focus()
     
            this.$refs.form.validate()
          } else {
            this.$refs.form.resetValidation()
          }
        }
      }
    },
    closeModal() { 
      this.$emit('closeModal')
    }
  }
}
</script>