<template>
  <div>
    <v-dialog
      v-model="dialog"
      fullscreen
      hide-overlay
    >
      <v-card tile>
        <v-toolbar
          flat
          dark
          elevation="2"
          color="blue darken-3"
          class="rounded-0"
        >
          <v-btn
            icon
            dark
            @click="closeModal"
          >
            <v-icon>mdi-close</v-icon>
          </v-btn>
          <v-toolbar-title>{{ (modalData.name.replace(/\s/g, '')) ? modalData.name : modalData.email }}</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-tooltip left>
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                v-bind="attrs"
                v-on="on"
                dark
                icon
                @click="updateStatus"
              >
                <v-icon>mdi-account-edit-outline</v-icon>
              </v-btn>
            </template>
            <span>Update Status</span>
          </v-tooltip>
          <template v-slot:extension>
            <v-tabs
              v-model="tab"
              align-with-title
            >
              <v-tabs-slider color="blue lighten-3"></v-tabs-slider>
              <v-tab>Applicant Info</v-tab>
              <v-tab>Exam Info</v-tab>
            </v-tabs>
          </template>
        </v-toolbar>
        <v-tabs-items v-model="tab">
          <v-tab-item class="mb-5">
            <v-row>
              <v-col cols="12" md="6">
                <!-- <p>{{ modalData}}</p> -->
                <p class="text-h6 mx-3 my-4">
                  APPLICATION INFORMATION
                  <v-tooltip
                    right
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        v-bind="attrs"
                        v-on="on"
                        @click="editApplicationInfo"
                        icon
                        small
                      >
                        <v-icon
                          size="large"
                          color="blue darken-3"
                        >
                          mdi-square-edit-outline
                        </v-icon>
                      </v-btn>
                    </template>
                    <span>Edit</span>
                  </v-tooltip>
                </p>
                <v-simple-table v-if="!viewLoading" dense>
                  <template v-slot:default>
                    <tbody>
                      <tr
                        v-for="item in applicationInfo"
                        :key="item.field"
                      >
                        <td class="text-caption" :style="{ width: tdWidth }">{{ item.field }}</td>
                        <td class="text-subtitle-2">
                          <v-btn
                            small 
                            depressed v-if="item.field=='Applicant Link'" 
                            color="blue darken-3 white--text"
                            @click="copyApplicantLink(item.link)"
                            ref="applicantLinkBtn"
                          >
                            <span>{{ applicantLinkText }}</span>
                            <!-- <v-icon
                              right
                              dark
                            >
                              mdi-clipboard-multiple-outline
                            </v-icon> -->
                          </v-btn>
                          <span v-else>{{ item.answer }}</span>
                          <v-tooltip
                            v-if="item.field=='Status'"
                            right
                          >
                            <template v-slot:activator="{ on, attrs }">
                              <v-btn
                                v-bind="attrs"
                                v-on="on"
                                @click="viewStatusHistory"
                                icon
                                small
                              >
                                <v-icon
                                  size="large"
                                  color="blue darken-3"
                                >
                                  mdi-clipboard-text-clock-outline
                                </v-icon>
                              </v-btn>
                            </template>
                            <span>View Status History</span>
                          </v-tooltip>
                        </td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
                <v-skeleton-loader
                  v-else
                  type="list-item-three-line"
                ></v-skeleton-loader>
                <p class="text-h6 mx-3 my-4">
                  PERSONAL INFORMATION
                  <v-tooltip
                    right
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        v-bind="attrs"
                        v-on="on"
                        @click="editPersonalInfo"
                        icon
                        small
                      >
                        <v-icon
                          size="large"
                          color="blue darken-3"
                        >
                          mdi-square-edit-outline
                        </v-icon>
                      </v-btn>
                    </template>
                    <span>Edit</span>
                  </v-tooltip>
                </p>
                <v-simple-table v-if="!viewLoading" dense>
                  <template v-slot:default>
                    <tbody>
                      <tr
                        v-for="item in personalInfo"
                        :key="item.field"
                      >
                        <td class="text-caption" :style="{ width: tdWidth }">{{ item.field }}</td>
                        <td class="text-subtitle-2">{{ item.answer }}</td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
                <v-skeleton-loader
                  v-else
                  type="list-item-three-line"
                ></v-skeleton-loader>
                <p class="text-h6 mx-3 my-4">EDUCATIONAL ATTAINMENT</p>
                <v-simple-table v-if="!viewLoading && viewData.form.education.length > 0" dense>
                  <template v-slot:default>
                    <tbody
                      v-for="(item, index) in viewData.form.education"
                      :key="index"
                    >
                      <tr>
                        <td colspan="2">
                          <v-chip>{{ index + 1 }}</v-chip>
                        </td>
                      </tr>
                      <tr>
                        <td class="text-caption" :style="{ width: tdWidth }">Level</td>
                        <td class="text-subtitle-2">{{ item.level }}</td>
                      </tr>
                      <tr>
                        <td class="text-caption">School</td>
                        <td class="text-subtitle-2">{{ item.school }}</td>
                      </tr>
                      <tr>
                        <td class="text-caption">Course</td>
                        <td class="text-subtitle-2">{{ item.course }}</td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
                <div v-else>
                  <v-skeleton-loader
                    v-if="viewLoading"
                    type="list-item-three-line"
                  ></v-skeleton-loader>
                  <v-simple-table v-else dense>
                    <template v-slot:default>
                      <tbody>
                        <tr>
                          <td colspan="2">-</td>
                        </tr>
                      </tbody>
                    </template>
                  </v-simple-table>
                </div>
                <v-row>
                  <v-col cols="12" md="6">
                    <p class="text-h6 mx-3 my-4">RESUME</p>
                    <v-simple-table v-if="!viewLoading" dense>
                      <template v-slot:default>
                        <tbody>
                          <tr>
                            <td class="text-caption" v-if="viewData.form.resume"><a :href="viewData.form.resume_link">Download File</a></td>
                            <td class="text-caption" v-else>No File</td>
                          </tr>
                        </tbody>
                      </template>
                    </v-simple-table>
                    <v-skeleton-loader
                      v-else
                      type="list-item"
                    ></v-skeleton-loader>
                  </v-col>
                  <v-col cols="12" md="6">
                    <p class="text-h6 mx-3 my-4">NBI CLEARANCE</p>
                    <v-simple-table v-if="!viewLoading" dense>
                      <template v-slot:default>
                        <tbody>
                          <tr>
                            <td class="text-caption" v-if="viewData.form.nbi_clearance"><a :href="viewData.form.nbi_link">Download File</a></td>
                            <td class="text-caption" v-else>No File</td>
                          </tr>
                        </tbody>
                      </template>
                    </v-simple-table>
                    <v-skeleton-loader
                      v-else
                      type="list-item"
                    ></v-skeleton-loader>
                  </v-col>
                </v-row>
              </v-col>
              <v-col cols="12" md="6">
                <p class="text-h6 mx-3 my-4">RECENT WORK EXPERIENCE</p>
                <v-simple-table v-if="!viewLoading && viewData.form.work.length > 0" dense>
                  <template v-slot:default>
                    <tbody
                      v-for="(item, index) in viewData.form.work"
                      :key="index"
                    >
                      <tr>
                        <td colspan="2">
                          <v-chip>{{ index + 1 }}</v-chip>
                        </td>
                      </tr>
                      <tr>
                        <td class="text-caption" :style="{ width: tdWidth }">Company</td>
                        <td class="text-subtitle-2">{{ item.company }}</td>
                      </tr>
                      <tr>
                        <td class="text-caption">Position</td>
                        <td class="text-subtitle-2">{{ item.position }}</td>
                      </tr>
                      <tr>
                        <td class="text-caption">Period</td>
                        <td class="text-subtitle-2">{{ item.period }}</td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
                <div v-else>
                  <v-skeleton-loader
                    v-if="viewLoading"
                    type="list-item-three-line"
                  ></v-skeleton-loader>
                  <v-simple-table v-else dense>
                    <template v-slot:default>
                      <tbody>
                        <tr>
                          <td colspan="2">-</td>
                        </tr>
                      </tbody>
                    </template>
                  </v-simple-table>
                </div>
                <p class="text-h6 mx-3 my-4">REAL STATE EXPERIENCE</p>
                <v-simple-table v-if="!viewLoading && viewData.form.real_state.length > 0" dense>
                  <template v-slot:default>
                    <tbody
                      v-for="(item, index) in viewData.form.real_state"
                      :key="index"
                    >
                      <tr>
                        <td colspan="2">
                          <v-chip>{{ index + 1 }}</v-chip>
                        </td>
                      </tr>
                      <tr>
                        <td class="text-caption" :style="{ width: tdWidth }">Company</td>
                        <td class="text-subtitle-2">{{ item.company }}</td>
                      </tr>
                      <tr>
                        <td class="text-caption">Inclusive Date From</td>
                        <td class="text-subtitle-2">{{ formatDate(item.inclusive_date_from) }}</td>
                      </tr>
                      <tr>
                        <td class="text-caption">Inclusive Date To</td>
                        <td class="text-subtitle-2">
                          {{ formatDate(item.inclusive_date_to) }} 
                          <span class="font-weight-light" v-if="diffMonths(item.inclusive_date_from, item.inclusive_date_to)">
                            ({{ diffMonths(item.inclusive_date_from, item.inclusive_date_to) }})
                          </span>
                        </td>
                      </tr>
                      <tr>
                        <td class="text-caption">Average Montly Production</td>
                        <td class="text-subtitle-2">{{ item.average_monthly_production }}</td>
                      </tr>
                      <tr>
                        <td class="text-caption">Reason for leaving</td>
                        <td class="text-subtitle-2">{{ item.reason_for_leaving }}</td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
                <div v-else>
                  <v-skeleton-loader
                    v-if="viewLoading"
                    type="list-item-three-line"
                  ></v-skeleton-loader>
                  <v-simple-table v-else dense>
                    <template v-slot:default>
                      <tbody>
                        <tr>
                          <td colspan="2">-</td>
                        </tr>
                      </tbody>
                    </template>
                  </v-simple-table>
                </div>
                <p class="text-h6 mx-3 my-4">WORK/APPLIED WITH NEW SAN JOSE BUILDERS, INC. BEFORE?</p>
                <div v-if="!viewLoading">
                  <v-simple-table dense>
                    <template v-slot:default>
                      <tbody>
                        <tr>
                          <td class="text-subtitle-2">{{ viewData.form.work_before_in_nsjbi || '-' }}</td>
                        </tr>
                      </tbody>
                    </template>
                  </v-simple-table>
                  <v-simple-table class="mb-5" dense v-if="viewData.form.work_before_in_nsjbi == 'Yes'">
                    <template v-slot:default>
                      <tbody>
                        <tr
                          v-for="(item, index) in wbinDetails"
                          :key="index"
                        >
                          <td class="text-caption" :style="{ width: tdWidth }">{{ item.field }}</td>
                          <td class="text-subtitle-2">
                            {{ item.answer }}
                            <span v-if="item.field=='Exclusive Date To'">
                              <span class="font-weight-light" v-if="diffMonths(viewData.form.exclusive_date_from, viewData.form.exclusive_date_to)">
                                ({{ diffMonths(viewData.form.exclusive_date_from, viewData.form.exclusive_date_to) }})
                              </span>
                            </span>
                          </td>
                        </tr>
                      </tbody>
                    </template>
                  </v-simple-table>
                </div>
                <v-skeleton-loader
                  v-else
                  type="list-item-three-line"
                ></v-skeleton-loader>
                <p class="text-h6 mx-3 my-4">PERSON TO NOTIFY IN CASE OF EMERGENCY</p>
                <v-simple-table v-if="!viewLoading" dense>
                  <template v-slot:default>
                    <tbody>
                      <tr
                        v-for="item in personToNotify"
                        :key="item.field"
                      >
                        <td class="text-caption" :style="{ width: tdWidth }">{{ item.field }}</td>
                        <td class="text-subtitle-2">{{ item.answer }}</td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
                <v-skeleton-loader
                  v-else
                  type="list-item-three-line"
                ></v-skeleton-loader>
                <p class="text-h6 mx-3 my-4">ENDORSEMENT DETAILS</p>
                <v-simple-table v-if="!viewLoading" dense>
                  <template v-slot:default>
                    <tbody>
                      <tr
                        v-for="item in endorsementDetails"
                        :key="item.field"
                      >
                        <td class="text-caption" :style="{ width: tdWidth }">{{ item.field }}</td>
                        <td class="text-subtitle-2">{{ item.answer }}</td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
                <v-skeleton-loader
                  v-else
                  type="list-item-three-line"
                ></v-skeleton-loader>
              </v-col>
            </v-row>
          </v-tab-item>
          <v-tab-item>
            <v-row>
              <v-col>
                <p class="text-h6 mx-3 my-4">EXAM INFORMATION</p>
                <v-simple-table v-if="!viewLoading" dense>
                  <template v-slot:default>
                    <thead>
                      <tr>
                        <th class="text-center" style="min-width: 100px">Action</th>
                        <th style="min-width: 250px">Test</th>
                        <th style="min-width: 95px">Time Limit</th>
                        <th style="min-width: 180px">Started At</th>
                        <th style="min-width: 180px">Expired At</th>
                        <th style="min-width: 180px">Finished At</th>
                        <th style="min-width: 110px">Passing Score</th>
                        <th style="min-width: 70px">Score</th>
                        <th style="min-width: 70px">Status</th>
                        <th style="min-width: 125px">Computation File</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr
                        v-for="item in viewData.exam"
                        :key="item.id"
                      >
                        <td class="text-center">
                          <v-tooltip right>
                            <template v-slot:activator="{ on, attrs }">
                              <v-btn
                                v-bind="attrs"
                                v-on="on"
                                @click="viewQuestions(item)"
                                icon
                                small
                              >
                                <v-icon
                                  size="large"
                                  color="grey darken-2"
                                >
                                  mdi-playlist-check
                                </v-icon>
                              </v-btn>
                            </template>
                            <span>View Questions</span>
                          </v-tooltip>
                          <TableGenerateScore :item="{ applicant_id: viewData.form.id, test_id: item.id }" v-if="item.result=='Expired'" @updateTest="updateTest" />
                        </td>
                        <td>{{ item.name }}</td>
                        <td>{{ item.time_limit }} <small class="grey--text">minute{{ parseInt(item.time_limit) > 1 ? 's' : '' }}</small></td>
                        <td>{{ formatDate(item.started_at, true) }}</td>
                        <td>{{ formatDate(item.expired_at, true) }}</td>
                        <td>{{ formatDate(item.finished_at, true) }}</td>
                        <td>{{ item.passing_score }}</td>
                        <td>{{ item.correct_answer }} / {{ parseInt(item.correct_answer) + parseInt(item.wrong_answer) }}</td>
                        <td><span :class="(item.result=='Passed') ? 'success--text' : 'red--text'">{{ item.result }}</span></td>
                        <td v-if="item.computation_file"><a :href="item.computation_link">Download File</a></td>
                        <td v-else>No File</td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
                <v-skeleton-loader
                  v-else
                  type="list-item-three-line"
                ></v-skeleton-loader>
              </v-col>
            </v-row>
          </v-tab-item>
        </v-tabs-items>
      </v-card>
    </v-dialog>
    <StatusHistoryModal v-if="statusHistoryModal" :modalData="statusHistoryModalData" @closeModal="statusHistoryModal = false" />
    <ViewQuestionsModal v-if="viewQuestionsModal" :modalData="viewQuestionsModalData" @closeModal="viewQuestionsModal = false" />
    <UpdateStatusModal v-if="updateStatusModal" :modalData="updateStatusModalData" @success="success" @closeModal="updateStatusModal = false" />
    <EditApplicationInfoModal v-if="editApplicationInfoModal" :modalData="editApplicationInfoModalData" @editedApplicationInfo="editedApplicationInfo" @closeModal="editApplicationInfoModal = false" />
    <EditPersonalInfoModal v-if="editPersonalInfoModal" :modalData="editPersonalInfoModalData" @editedPersonalInfo="editedPersonalInfo" @closeModal="editPersonalInfoModal = false" />
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex"
import { format, differenceInMonths } from 'date-fns'
import StatusHistoryModal from "@/components/applicants/StatusHistoryModal"
import ViewQuestionsModal from "@/components/applicants/ViewQuestionsModal"
import UpdateStatusModal from "@/components/applicants/UpdateStatusModal"
import EditApplicationInfoModal from "@/components/applicants/EditApplicationInfoModal"
import EditPersonalInfoModal from "@/components/applicants/EditPersonalInfoModal"
import TableGenerateScore from "@/components/applicants/TableGenerateScore"

export default {
  components: { StatusHistoryModal, ViewQuestionsModal, UpdateStatusModal, EditApplicationInfoModal, EditPersonalInfoModal, TableGenerateScore },
  props: {
    modalData: {
      type: Object,
      required: true
    }
  },
  data: () => ({
    tab: 0,
    tdWidth: '25%',
    applicantLinkText: 'Copy the link',
    statusHistoryModal: false,
    statusHistoryModalData: [],
    viewQuestionsModal: false,
    viewQuestionsModalData: [],
    updateStatusModal: false,
    updateStatusModalData: [],
    editApplicationInfoModal: false,
    editApplicationInfoModalData: [],
    editPersonalInfoModal: false,
    editPersonalInfoModalData: [],
  }),
  created() {
    if (!this.viewLoading) {
      this.$store.commit("applicants/setViewData", [])
      this.getViewData({ applicant_id: this.modalData.id })
    }
  },
  mounted() {
    this.tab = this.modalData.info
  },
  computed: {
    ...mapState("applicants", ["viewData", "viewError", "viewLoading"]),
    dialog: {
      get: function () {
        return this.modalData.dialog
      },
      // setter
      set: function () {
        this.closeModal()
      }
    },
    applicationInfo() {
      return [
        {
          field: 'Applicant Link',
          answer: 'Copy the link',
          link: this.viewData.form.applicant_link
        },
        {
          field: 'Sales Group',
          answer: this.viewData.form.sales_group
        },
        {
          field: 'Position',
          answer: this.viewData.form.position
        },
        {
          field: 'Date of Application',
          answer: this.formatDate(this.viewData.form.date_of_application)
        },
        {
          field: 'Status',
          answer: this.viewData.form.applicant_status
        },
        {
          field: 'Added By',
          answer: this.viewData.form.added_by
        },
        {
          field: 'Created At',
          answer: this.formatDate(this.viewData.form.created_at, true)
        },
        /* {
          field: 'Updated At',
          answer: this.formatDate(this.viewData.form.updated_at, true)
        }, */
      ]
    },
    personalInfo() {
      return [
        {
          field: 'Surname',
          answer: this.viewData.form.surname
        },
        {
          field: 'Given Name',
          answer: this.viewData.form.given_name
        },
        {
          field: 'Middle Name',
          answer: this.viewData.form.middle_name
        },
        {
          field: 'Nickname',
          answer: this.viewData.form.nickname
        },
        {
          field: 'Address',
          answer: this.viewData.form.address
        },
        {
          field: 'Birthdate',
          answer: this.formatDate(this.viewData.form.birthdate)
        },
        {
          field: 'Age',
          answer: (this.viewData.form.age == '0') ? '' : this.viewData.form.age
        },
        {
          field: 'Gender',
          answer: this.viewData.form.gender
        },
        {
          field: 'Civil Status',
          answer: this.viewData.form.civil_status
        },
        {
          field: 'Contact Number',
          answer: this.viewData.form.contact
        },
        {
          field: 'Email Address',
          answer: this.viewData.form.email
        },
        {
          field: 'TIN',
          answer: this.viewData.form.tin
        },
        {
          field: 'Updated At',
          answer: this.formatDate(this.viewData.form.updated_at, true)
        },
      ]
    },
    personToNotify() {
      return [
        {
          field: 'Name',
          answer: this.viewData.form.contact_name
        },
        {
          field: 'Contact Number',
          answer: this.viewData.form.contact_mobile_number
        },
        {
          field: 'Address',
          answer: this.viewData.form.contact_address
        }
      ]
    },
    wbinDetails() {
      return [
        {
          field: 'What focus project?',
          answer: this.viewData.form.if_yes_what_project
        },
        {
          field: 'Exclusive Date From',
          answer: this.formatDate(this.viewData.form.exclusive_date_from)
        },
        {
          field: 'Exclusive Date To',
          answer: this.formatDate(this.viewData.form.exclusive_date_to)
        },
        {
          field: 'Monthly Sales Production',
          answer: this.viewData.form.monthly_sales
        },
        {
          field: 'Position',
          answer: this.viewData.form.before_position
        },
        {
          field: 'Sales Group / Head',
          answer: this.viewData.form.sales_group_head
        }
      ]
    },
    endorsementDetails() {
      return [
        {
          field: 'Sales Head',
          answer: this.viewData.form.sales_head
        },
        {
          field: 'Project Director',
          answer: this.viewData.form.project_director
        },
        {
          field: 'Sales Director',
          answer: this.viewData.form.sales_director 
        }
      ]
    },
  },
  methods: {
    ...mapActions("applicants", ["getViewData"]),
    formatDate(date, isTime = false) {
      if (date) {
        if (isTime) {
          return format(new Date(date), 'MMM dd, yyyy hh:mm a')
        } else {
          return format(new Date(date), 'MMM dd, yyyy')
        }
      } else {
        return '-'
      }
    },
    diffMonths(from, to) {
      if (from && to) {
        const totalMonths = differenceInMonths(new Date(to), new Date(from))

        if (totalMonths > 0) {
          if (totalMonths > 11) {
            const oneYear = 12
            const yearCount = Math.floor(totalMonths / oneYear)
            const yearMonthCount = Math.floor(yearCount * oneYear)
            const remainingMonth = Math.floor(totalMonths - yearMonthCount)

            if (remainingMonth) {
              return yearCount + ' year' + this.withEs(yearCount) + ' and ' + remainingMonth + ' month' + this.withEs(remainingMonth)
            } else {
              return yearCount + ' year' + this.withEs(yearCount)
            }
          } else {
            return totalMonths + ' month' + this.withEs(totalMonths)
          }
        } else {
          return ''
        }
      }
    },
    withEs(num) {
      return num > 1 ? 's' : ''
    },
    copyApplicantLink(link) {
      navigator.clipboard.writeText(link);  

      this.applicantLinkText = 'Copied'
      // console.log(this.$refs['applicantLinkBtn'][0].$el.classList.remove('blue'))
      this.$refs['applicantLinkBtn'][0].$el.classList.remove('blue')
      this.$refs['applicantLinkBtn'][0].$el.classList.add('green')
      // this.$refs.applicantLinkBtn.$el.classList.add('green')

      setTimeout(() => {
        this.applicantLinkText = 'Copy the link'

        this.$refs['applicantLinkBtn'][0].$el.classList.remove('green')
        this.$refs['applicantLinkBtn'][0].$el.classList.add('blue')
      }, 1000)
    },
    viewStatusHistory() {
      this.statusHistoryModal = true,
      this.statusHistoryModalData = { status_history: this.viewData.status_history, dialog: this.statusHistoryModal }
    },
    viewQuestions(exam) {
      this.viewQuestionsModal = true,
      this.viewQuestionsModalData = { exam: exam, dialog: this.viewQuestionsModal }
    },
    updateStatus() {
      this.updateStatusModal = true
      this.updateStatusModalData = { ...this.modalData, dialog: this.updateStatusModal }
    },
    editApplicationInfo() {
      this.editApplicationInfoModal = true
      this.editApplicationInfoModalData = { ...this.viewData.form, dialog: this.editApplicationInfoModal }
    },
    editPersonalInfo() {
      this.editPersonalInfoModal = true
      this.editPersonalInfoModalData = { ...this.viewData.form, dialog: this.editPersonalInfoModal }
    },
    editedApplicationInfo(newInfo) {
      Object.entries(newInfo.data).forEach(([key, value]) => { 
        this.viewData.form[key] = value
      })

      const forMainData = {}

      if (newInfo.data.sales_group) {
        forMainData['sales_group'] = this.viewData.form.sales_group
      }

      if (newInfo.data.position) {
        forMainData['position'] = this.viewData.form.position
      }
      
      this.$emit('updateData', this.modalData.id, forMainData)
    },
    editedPersonalInfo(newInfo) {
      Object.entries(newInfo.data).forEach(([key, value]) => { 
        if (key=='mobile_number') {
          this.viewData.form['contact'] = value
        } else {
          this.viewData.form[key] = value
        }
      })

      const forMainData = {}

      if (newInfo.data.surname || newInfo.data.given_name || newInfo.data.middle_name) {
        forMainData['name'] = this.viewData.form.given_name + ' ' + this.viewData.form.middle_name + ' ' + this.viewData.form.surname
      }

      if (newInfo.data.email) {
        forMainData['email'] = this.viewData.form.email
      }

      if (newInfo.data.mobile_number) {
        forMainData['contact'] = this.viewData.form.contact
      }
      
      this.$emit('updateData', this.modalData.id, forMainData)
    },
    success(id, text) {
      this.viewData.form.applicant_status = text
      this.$emit('updatedStatus', this.modalData.id, id, text)
    },
    updateTest(id, result, correct_answer, wrong_answer){
      this.viewData.exam.find((test) => test.id == id).result = result
      this.viewData.exam.find((test) => test.id == id).correct_answer = correct_answer
      this.viewData.exam.find((test) => test.id == id).wrong_answer = wrong_answer
    },
    closeModal() { 
      this.$emit('closeModal')
    }
  }
}
</script>