import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '../store'
import Home from '../views/Home.vue'
import Applicants from '../views/Applicants.vue'

Vue.use(VueRouter)

const guest = (to, from, next) => {
  if (!store.getters.isAuthenticated) {
    return next()
  } else {
    return next("/dashboard")
  }
}
const auth = (to, from, next) => {
  if (store.getters.isAuthenticated) {
    return next()
  } else {
    return next("/")
  }
}

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    beforeEnter: guest
  },
  {
    path: '/dashboard',
    name: 'Dashboard',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "dashboard" */ '../views/Dashboard.vue'),
    beforeEnter: auth
  },
  {
    path: '/applicants',
    name: 'Applicants',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: Applicants,
    // props: route => ({ query: route.query }),
    beforeEnter: auth
  },
  {
    path: '/questions',
    name: 'Questions',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "questions" */ '../views/Questions.vue'),
    beforeEnter: auth
  },
  {
    path: '/users',
    name: 'Users',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "users" */ '../views/Users.vue'),
    beforeEnter: auth
  },
  {
    path: '/settings',
    name: 'Settings',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "settings" */ '../views/Settings.vue'),
    beforeEnter: auth
  },
  {
    path: '/tests',
    name: 'Tests',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "tests" */ '../views/Tests.vue'),
    beforeEnter: auth
  },
  {
    path: '/tests/:id',
    name: 'TestSettings',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "testSettings" */ '../views/TestSettings.vue'),
    beforeEnter: auth,
    props: true,
    meta: {
      title: 'Test Settings'
    }
  },
  {
    path: '/applicant/:code',
    name: 'Applicant',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "applicant" */ '../views/Applicant.vue'),
    props: true
  },
  // catchall 404
  {
    path: '/:catchAll(.*)',
    name: 'NotFound',
    component: () => import(/* webpackChunkName: "applicant" */ '../views/NotFound.vue'),
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
