const url = process.env.VUE_APP_API_URL + "settings/"

export default {
  namespaced: true,

  state: {
    moduleData: [],
    moduleDataLoading: false,
    moduleDataError: null,
    saveExamTimeLoading: false,
    saveExamTimeError: null,
    saveExamCountLoading: false,
    saveExamCountError: null,
    saveExamPassingScoreLoading: false,
    saveExamPassingScoreError: null,
  },
  
  mutations: {
    setModuleData (state, data) {
      state.moduleData = data
    },
    setModuleDataLoading (state, data) {
      state.moduleDataLoading = data
    },
    setModuleDataError (state, data) {
      state.moduleDataError = data
    },
    setSaveExamTimeLoading (state, data) {
      state.saveExamTimeLoading = data
    },
    setSaveExamTimeError (state, data) {
      state.saveExamTimeError = data
    },
    setSaveExamCountLoading (state, data) {
      state.saveExamCountLoading = data
    },
    setSaveExamCountError (state, data) {
      state.saveExamCountError = data
    },
    setSaveExamPassingScoreLoading (state, data) {
      state.saveExamPassingScoreLoading = data
    },
    setSaveExamPassingScoreError (state, data) {
      state.saveExamPassingScoreError = data
    },
  },

  actions: {
    async getModuleData({ commit, rootState }) {
      commit("setModuleDataLoading", true)

      try {
        const response = await fetch(`${url}get`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${rootState.userToken}`,
          },
        });

        const res = await response.json()
        
        if (!response.ok) {
          throw { response, res }
        } else {
          commit("setModuleData", res.records)
        }
      } catch(error) {
        const errorStatus = error.response.status
        
        if (errorStatus==401) {
          commit("setError", "Unauthorized", { root: true })
        } else {
          commit("setRefreshError", "Submitted invalid data", { root: true })
        }
      } finally {
        commit("setModuleDataLoading", false)
      }
    },

    async saveExamTime({ commit, rootState }, data) {
      commit("setSaveExamTimeLoading", true)

      try {
        const response = await fetch(`${url}save_exam_time`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${rootState.userToken}`,
          },
          body: JSON.stringify(data)
        });

        const res = await response.json()
        
        if (!response.ok) {
          throw { response, res }
        } else {
          commit("setSaveExamTimeError", null) 
        }
      } catch(error) {
        const errorStatus = error.response.status
        
        if (errorStatus==424) {
          commit("setSaveExamTimeError", error.res.error)
        } else if (errorStatus==401) {
          commit("setError", "Unauthorized", { root: true }) // force logout
        } else {
          commit("setPopUpError", "Something went wrong, please try again!", { root: true })
        }
      } finally {
        commit("setSaveExamTimeLoading", false)
      }
    },

    async saveExamCount({ commit, rootState }, data) {
      commit("setSaveExamCountLoading", true)

      try {
        const response = await fetch(`${url}save_exam_count`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${rootState.userToken}`,
          },
          body: JSON.stringify(data)
        });

        const res = await response.json()
        
        if (!response.ok) {
          throw { response, res }
        } else {
          commit("setSaveExamCountError", null) 
        }
      } catch(error) {
        const errorStatus = error.response.status
        
        if (errorStatus==424) {
          commit("setSaveExamCountError", error.res.error)
        } else if (errorStatus==401) {
          commit("setError", "Unauthorized", { root: true }) // force logout
        } else {
          commit("setPopUpError", "Something went wrong, please try again!", { root: true })
        }
      } finally {
        commit("setSaveExamCountLoading", false)
      }
    },

    async saveExamPassingScore({ commit, rootState }, data) {
      commit("setSaveExamPassingScoreLoading", true)

      try {
        const response = await fetch(`${url}save_exam_passing_score`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${rootState.userToken}`,
          },
          body: JSON.stringify(data)
        });

        const res = await response.json()
        
        if (!response.ok) {
          throw { response, res }
        } else {
          commit("setSaveExamPassingScoreError", null) 
        }
      } catch(error) {
        const errorStatus = error.response.status
        
        if (errorStatus==424) {
          commit("setSaveExamPassingScoreError", error.res.error)
        } else if (errorStatus==401) {
          commit("setError", "Unauthorized", { root: true }) // force logout
        } else {
          commit("setPopUpError", "Something went wrong, please try again!", { root: true })
        }
      } finally {
        commit("setSaveExamPassingScoreLoading", false)
      }
    },
  }
}