const formRules = {
  required: v => !!v || 'This field is required.',
  minimum: (min) => { return v => !v || (v || '').length >= min || `Minimum ${min} characters.` },
  maximum: (max) => { return v => !v || (v || '').length <= max || `Maximum ${max} characters.` },
  numeric: v => {
    if (v) return /^\d+$/.test(v) || 'This field must contain only numbers.'
    else return true
  },
  alphaSpace: v => {
    if (v) return /^[a-zA-Z ]*$/.test(v) || 'This field may only contain alphabetical characters and spaces.'
    else return true
  },
  validEmail: v => {
    if (v) return /.+@.+\..+/.test(v) || 'E-mail must be valid'
    else return true
  },
  validMobileNumber: v => {
    if (v) return /^(09)\d{9}$/.test(v) || 'Invalid format, example: 09293158777'
    else return true
  },
  resumeSize: v => !v || v.size < 1000000 || 'File size must not exceed 1MB',
  fileSize: (fileSize) => { return v => !v || v.size < (fileSize * 1000000) || `File size must not exceed ${fileSize}MB, you have selected a file with ${(v.size / 1000000).toFixed(2)}MB size.` },
}

export default formRules