const url = process.env.VUE_APP_API_URL + "account/"

export default {
  namespaced: true,

  state: {
    changePasswordLoading: false,
    changePasswordError: null,
    changePasswordErrors: [],
  },

  mutations: {
    setChangePasswordLoading (state, val) {
      state.changePasswordLoading = val
    },
    setChangePasswordError (state, error) {
      state.changePasswordError = error
    },
    setChangePasswordErrors (state, errors) {
      state.changePasswordErrors = errors
    },
  },

  actions: {
    async changePassword({ commit, rootState }, data) {
      commit("setChangePasswordLoading", true)

      try {
        const response = await fetch(`${url}change_password`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${rootState.userToken}`,
          },
          body: JSON.stringify(data)
        });

        const res = await response.json()
        
        if (!response.ok) {
          throw { response, res }
        } else {
          commit("setChangePasswordErrors", []) 
          commit("setChangePasswordError", null) 
        }
      } catch(error) {
        const errorStatus = error.response.status
        
        if (errorStatus==422) {
          commit("setChangePasswordErrors", error.res.messages)
          commit("setChangePasswordError", null) 
        } else if (errorStatus==424) {
          commit("setChangePasswordErrors", [])
          commit("setChangePasswordError", error.res.error)
        } else if (errorStatus==401) {
          commit("setError", "Unauthorized", { root: true }) // force logout
        } else {
          commit("setPopUpError", "Something went wrong, please try again!", { root: true })
        }
      } finally {
        commit("setChangePasswordLoading", false)
      }
    },
  }
}