const url = process.env.VUE_APP_API_URL + "applicant/"

export default {
  namespaced: true,

  state: {
    details: [],
    isLoading: false,
    error: null,
    isFormLoading: false,
    formError: null,
    formErrors: [],
    startLoading: false,
    startError: null,
    clickAnswerLoading: false,
    clickAnswerError: null,
    formAnswerLoading: false,
    formAnswerError: null,
    uploadLoading: false,
    uploadError: null
  },

  mutations: {
    setDetails (state, data) {
      state.details = data
    },
    setIsLoading (state, val) {
      state.isLoading = val
    },
    setError (state, error) {
      state.error = error
    },
    setIsFormLoading (state, val) {
      state.isFormLoading = val
    },
    setFormError (state, error) {
      state.formError = error
    },
    setFormErrors (state, errors) {
      state.formErrors = errors
    },
    setStartLoading (state, val) {
      state.startLoading = val
    },
    setStartError (state, error) {
      state.startError = error
    },
    setClickAnswerLoading (state, val) {
      state.clickAnswerLoading = val
    },
    setClickAnswerError (state, error) {
      state.clickAnswerError = error
    },
    setFormAnswerLoading (state, val) {
      state.formAnswerLoading = val
    },
    setFormAnswerError (state, error) {
      state.formAnswerError = error
    },
    setUploadLoading (state, val) {
      state.uploadLoading = val
    },
    setUploadError (state, error) {
      state.uploadError = error
    },
    reset (state) {
      state.details = []
      state.isLoading = false
      state.error = null,
      state.isFormLoading = false
      state.formError = null
      state.formErrors = [],
      state.startLoading = false,
      state.startError = null,
      state.clickAnswerLoading = false,
      state.clickAnswerError = null
    },
  },

  actions: {
    async getDetails({ commit }, code) {
      commit("setIsLoading", true)

      try {
        const response = await fetch(`${url}get`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({ code: code })
        });

        const res = await response.json()
        
        if (!response.ok) {
          throw { response, res }
        } else {
          commit("setDetails", res)
        }
      } catch(error) {
        commit("setError", "Something went wrong, please refresh your page and try again!")
      } finally {
        commit("setIsLoading", false)
      }
    },

    async formSubmit({ commit }, data) {
      commit("setIsFormLoading", true)

      try {
        const formData = new FormData()

        for (var key in data) {
          if (['education', 'work_exp', 'state_exp'].includes(key)) {
            formData.append(key, JSON.stringify(data[key]))
          } else {
            formData.append(key, data[key])
          }
        }

        const response = await fetch(`${url}form`, {
          method: 'POST',
          /* headers: {
            'Content-Type': 'application/json',
          }, */
          body: formData//JSON.stringify(data)
        });

        const res = await response.json()
        
        if (!response.ok) {
          throw { response, res }
        } else {
          commit("setFormErrors", []) 
          commit("setFormError", null) 
        }
      } catch(error) {
        const errorStatus = error.response.status
        
        if (errorStatus==422) {
          commit("setFormErrors", error.res.messages)
          commit("setFormError", null) 
        } else {
          // Status code 424
          commit("setFormErrors", [])
          commit("setFormError", error.res.error)
        }
      } finally {
        commit("setIsFormLoading", false)
      }
    },

    async startExam({ commit }, data) {
      commit("setStartLoading", true)

      try {
        const response = await fetch(`${url}start`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(data)
        });

        const res = await response.json()
        
        if (!response.ok) {
          throw { response, res }
        } else {
          commit("setStartError", null)

          return res
        }
      } catch(error) {
        commit("setStartError", error.res.error)
      } finally {
        commit("setStartLoading", false)
      }
    },

    async clickAnswer({ commit }, data) {
      commit("setClickAnswerLoading", true)

      try {
        const response = await fetch(`${url}answer`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(data)
        });

        const res = await response.json()
        
        if (!response.ok) {
          throw { response, res }
        } else {
          commit("setClickAnswerError", null) 

          return res
        }
      } catch(error) {
        commit("setClickAnswerError", "An error occured, please refresh your page and try again!")
      } finally {
        commit("setClickAnswerLoading", false)
      }
    },

    async formAnswer({ commit }, data) {
      commit("setFormAnswerLoading", true)

      try {
        const response = await fetch(`${url}form_answer`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(data)
        });

        const res = await response.json()
        
        if (!response.ok) {
          throw { response, res }
        } else {
          commit("setFormAnswerError", null) 

          return res
        }
      } catch(error) {
        commit("setFormAnswerError", "An error occured, please refresh your page and try again!")
      } finally {
        commit("setFormAnswerLoading", false)
      }
    },

    async uploadSubmit({ commit }, data) {
      commit("setUploadLoading", true)

      try {
        const formData = new FormData()

        for (var key in data) {
          formData.append(key, data[key])
        }

        const response = await fetch(`${url}upload_computation`, {
          method: 'POST',
          body: formData
        });

        const res = await response.json()
        
        if (!response.ok) {
          throw { response, res }
        } else {
          commit("setUploadError", null) 

          return res
        }
      } catch(error) {
        commit("setUploadError", "An error occured, please refresh your page and try again!")
      } finally {
        commit("setUploadLoading", false)
      }
    },
  }
}