const url = process.env.VUE_APP_API_URL + "applicants/"

export default {
  namespaced: true,

  state: {
    records: [],
    isLoading: false,
    addLoading: false,
    addError: null,
    addErrors: [],
    viewLoading: false,
    viewError: null,
    viewData: [],
    statusLoading: false,
    statusData: [],
    updateStatusLoading: false,
    updateStatusError: null,
    updateStatusErrors: [],
    editApplicationInfoLoading: false,
    editApplicationInfoError: null,
    editApplicationInfoErrors: [],
    editPersonalInfoLoading: false,
    editPersonalInfoError: null,
    editPersonalInfoErrors: [],
    salesGroups: []
  },

  mutations: {
    setRecords (state, data) {
      state.records = data
    },
    setIsLoading (state, val) {
      state.isLoading = val
    },
    setAddLoading (state, val) {
      state.addLoading = val
    },
    setAddError (state, error) {
      state.addError = error
    },
    setAddErrors (state, errors) {
      state.addErrors = errors
    },
    setViewLoading (state, val) {
      state.viewLoading = val
    },
    setViewError (state, error) {
      state.viewError = error
    },
    setViewData (state, data) {
      state.viewData = data
    },
    setStatusLoading (state, val) {
      state.statusLoading = val
    },
    setStatusData (state, data) {
      state.statusData = data
    },
    setUpdateStatusLoading (state, val) {
      state.updateStatusLoading = val
    },
    setUpdateStatusError (state, error) {
      state.updateStatusError = error
    },
    setUpdateStatusErrors (state, errors) {
      state.updateStatusErrors = errors
    },
    setEditApplicationInfoLoading (state, val) {
      state.editApplicationInfoLoading = val
    },
    setEditApplicationInfoError (state, error) {
      state.editApplicationInfoError = error
    },
    setEditApplicationInfoErrors (state, errors) {
      state.editApplicationInfoErrors = errors
    },
    setEditPersonalInfoLoading (state, val) {
      state.editPersonalInfoLoading = val
    },
    setEditPersonalInfoError (state, error) {
      state.editPersonalInfoError = error
    },
    setEditPersonalInfoErrors (state, errors) {
      state.editPersonalInfoErrors = errors
    },
    setSalesGroups (state, data) {
      state.salesGroups = data
    },
  },

  actions: {
    async getApplicants({ commit, state, rootState }, data) {
      commit("setIsLoading", true)

      try {
        const response = await fetch(`${url}get`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${rootState.userToken}`,
          },
          body: JSON.stringify({ 
            status: data.status,
            created_at_start: data.createdAt[0], 
            created_at_end: data.createdAt[1] || data.createdAt[0],
            date_of_applicant_start: data.dateOfApplication[0], 
            date_of_applicant_end: data.dateOfApplication[1] || data.dateOfApplication[0],
            status_updated_at_start: data.statusUpdatedAt[0], 
            status_updated_at_end: data.statusUpdatedAt[1] || data.statusUpdatedAt[0],
            sales_group: data.salesGroup,
            position: data.position,
            get_sales_groups: (!state.salesGroups.length) ? true : false
          })
        });

        const res = await response.json()
        
        if (!response.ok) {
          throw { response, res }
        } else {
          commit("setRecords", res.records)

          if (res.salesGroups) {
            commit("setSalesGroups", res.salesGroups)
          }
        }
      } catch(error) {
        const errorStatus = error.response.status
        
        if (errorStatus==401) {
          commit("setError", "Unauthorized", { root: true })
        } else {
          commit("setRefreshError", "Submitted invalid data", { root: true })
        }
      } finally {
        commit("setIsLoading", false)
      }
    },

    async addApplicant({ commit, rootState }, data) {
      commit("setAddLoading", true)

      try {
        const response = await fetch(`${url}add`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${rootState.userToken}`,
          },
          body: JSON.stringify(data)
        });

        const res = await response.json()
        
        if (!response.ok) {
          throw { response, res }
        } else {
          commit("setAddErrors", []) 
          commit("setAddError", null) 
        }
      } catch(error) {
        const errorStatus = error.response.status
        
        if (errorStatus==422) {
          commit("setAddErrors", error.res.messages)
          commit("setAddError", null) 
        } else if (errorStatus==424) {
          commit("setAddErrors", [])
          commit("setAddError", error.res.error)
        } else if (errorStatus==401) {
          commit("setError", "Unauthorized", { root: true }) // force logout
        } else {
          commit("setPopUpError", "Something went wrong, please try again!", { root: true })
        }
      } finally {
        commit("setAddLoading", false)
      }
    },

    async getViewData({ commit, rootState }, data) {
      commit("setViewLoading", true)

      try {
        const response = await fetch(`${url}view_data`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${rootState.userToken}`,
          },
          body: JSON.stringify(data)
        });

        const res = await response.json()
        
        if (!response.ok) {
          throw { response, res }
        } else {
          commit("setViewData", res.data) 
          commit("setViewError", null) 
        }
      } catch(error) {
        const errorStatus = error.response.status
        
        if (errorStatus==424) {
          commit("setViewError", error.res.error)
        } else {
          commit("setError", "Unauthorized", { root: true })
        }
      } finally {
        commit("setViewLoading", false)
      }
    },

    async doSenDLink({ commit, rootState }, data) {
      try {
        const response = await fetch(`${url}send_link`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${rootState.userToken}`,
          },
          body: JSON.stringify(data)
        });

        const res = await response.json()
        
        if (!response.ok) {
          throw { response, res }
        }
      } catch(error) {
        const errorStatus = error.response.status
        
        if (errorStatus==401) {
          commit("setError", "Unauthorized", { root: true }) // force logout
        } else {
          commit("setPopUpError", "Something went wrong, please try again!", { root: true })
        }
      }
    },

    async getStatusData({ commit, rootState }, data) {
      commit("setStatusLoading", true)

      try {
        const response = await fetch(`${url}status_data`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${rootState.userToken}`,
          },
          body: JSON.stringify(data)
        });

        const res = await response.json()
        
        if (!response.ok) {
          throw { response, res }
        } else {
          commit("setStatusData", res.data) 
        }
      } catch(error) {
        const errorStatus = error.response.status
        
        if (errorStatus==404) {
          // failed makuha yung status, something went wrong na lng na error at refresh page
          commit("setRefreshError", "Something went wrong!", { root: true })
        } else if (errorStatus==401) {
          commit("setError", "Unauthorized", { root: true }) // force logout
        } else {
          commit("setPopUpError", "Something went wrong, please try again!", { root: true })
        }
      } finally {
        commit("setStatusLoading", false)
      }
    },

    async updateStatus({ commit, rootState }, data) {
      commit("setUpdateStatusLoading", true)

      try {
        const response = await fetch(`${url}update_status`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${rootState.userToken}`,
          },
          body: JSON.stringify(data)
        });

        const res = await response.json()
        
        if (!response.ok) {
          throw { response, res }
        } else {
          commit("setUpdateStatusErrors", []) 
          commit("setUpdateStatusError", null) 
        }
      } catch(error) {
        const errorStatus = error.response.status
        
        if (errorStatus==422) {
          commit("setUpdateStatusErrors", error.res.messages)
          commit("setUpdateStatusError", null) 
        } else if (errorStatus==424) {
          commit("setUpdateStatusErrors", [])
          commit("setUpdateStatusError", error.res.error)
        } else if (errorStatus==401) {
          commit("setError", "Unauthorized", { root: true }) // force logout
        } else {
          commit("setPopUpError", "Something went wrong, please try again!", { root: true })
        }
      } finally {
        commit("setUpdateStatusLoading", false)
      }
    },

    async editApplicationInfo({ commit, rootState }, data) {
      commit("setEditApplicationInfoLoading", true)

      try {
        const response = await fetch(`${url}edit_application_info`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${rootState.userToken}`,
          },
          body: JSON.stringify(data)
        });

        const res = await response.json()
        
        if (!response.ok) {
          throw { response, res }
        } else {
          commit("setEditApplicationInfoErrors", []) 
          commit("setEditApplicationInfoError", null) 

          return res
        }
      } catch(error) {
        const errorStatus = error.response.status
        
        if (errorStatus==422) {
          commit("setEditApplicationInfoErrors", error.res.messages)
          commit("setEditApplicationInfoError", null) 
        } else if (errorStatus==424) {
          commit("setEditApplicationInfoErrors", [])
          commit("setEditApplicationInfoError", error.res.error)
        } else if (errorStatus==401) {
          commit("setError", "Unauthorized", { root: true }) // force logout
        } else {
          commit("setPopUpError", "Something went wrong, please try again!", { root: true })
        }
      } finally {
        commit("setEditApplicationInfoLoading", false)
      }
    },

    async editPersonalInfo({ commit, rootState }, data) {
      commit("setEditPersonalInfoLoading", true)

      try {
        const response = await fetch(`${url}edit_personal_info`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${rootState.userToken}`,
          },
          body: JSON.stringify(data)
        });

        const res = await response.json()
        
        if (!response.ok) {
          throw { response, res }
        } else {
          commit("setEditPersonalInfoErrors", []) 
          commit("setEditPersonalInfoError", null) 

          return res
        }
      } catch(error) {
        const errorStatus = error.response.status
        
        if (errorStatus==422) {
          commit("setEditPersonalInfoErrors", error.res.messages)
          commit("setEditPersonalInfoError", null) 
        } else if (errorStatus==424) {
          commit("setEditPersonalInfoErrors", [])
          commit("setEditPersonalInfoError", error.res.error)
        } else if (errorStatus==401) {
          commit("setError", "Unauthorized", { root: true }) // force logout
        } else {
          commit("setPopUpError", "Something went wrong, please try again!", { root: true })
        }
      } finally {
        commit("setEditPersonalInfoLoading", false)
      }
    },

    async generateTestScore({ commit, rootState }, data) {
      try {
        const response = await fetch(`${url}generate_test_score`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${rootState.userToken}`,
          },
          body: JSON.stringify(data)
        });

        const res = await response.json()
        
        if (!response.ok) {
          throw { response, res }
        } else {
          return res
        }
      } catch(error) {
        const errorStatus = error.response.status
        
        if (errorStatus==401) {
          commit("setError", "Unauthorized", { root: true }) // force logout
        } else {
          commit("setPopUpError", "Something went wrong, please try again!", { root: true })
        }
      }
    },

    async doSendInterview({ commit, rootState }, data) {
      try {
        const response = await fetch(`${url}send_interview`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${rootState.userToken}`,
          },
          body: JSON.stringify(data)
        });

        const res = await response.json()
        
        if (!response.ok) {
          throw { response, res }
        }
      } catch(error) {
        const errorStatus = error.response.status
        
        if (errorStatus==401) {
          commit("setError", "Unauthorized", { root: true }) // force logout
        } else {
          commit("setPopUpError", "Something went wrong, please try again!", { root: true })
        }
      }
    },
  }
}