<template>
  <v-dialog
    v-model="dialog"
    width="700"
  >
    <v-card>
      <v-card-title>
        <span class="text-h6">Status History</span>
        <v-spacer></v-spacer>
        <v-btn
          icon
          @click="closeModal"
        >
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-card-title>
      <v-divider></v-divider>
      <v-card-text class="pt-3 pb-6">
        <v-timeline
          dense
          clipped
        >
          <v-timeline-item
            color="blue darken-3"
            small
            v-for="item in statusHistory"
            :key="item.updated_at"
          >
            <v-row class="pt-1">
              <v-col cols="5">
                <strong>{{ item.formattedUpdatedAt }}</strong>
              </v-col>
              <v-col>
                <strong>{{ item.status }}</strong>
                <div v-if="item.status_tag" class="font-weight-bold mb-2">{{ item.status_tag }}</div>
                <div v-if="item.remarks" class="text-caption mt-3">
                  {{ item.remarks }}
                </div>
                <div v-if="item.updated_by" class="text-body-2 mt-2">
                  <v-icon x-small>mdi-account</v-icon> {{ item.updated_by }}
                </div>
              </v-col>
            </v-row>
          </v-timeline-item>
        </v-timeline>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
import { format } from 'date-fns'

export default {
  props: {
    modalData: {
      type: Object,
      required: true
    }
  },
  data: () => ({
    // 
    
  }),
  computed: {
    dialog: {
      get: function () {
        return this.modalData.dialog
      },
      // setter
      set: function () {
        this.closeModal()
      }
    },
    statusHistory() {
      return this.modalData.status_history.map(history => {
        return {...history, formattedUpdatedAt: format(new Date(history.updated_at), 'MMM dd, yyyy hh:mm a')}
      })
    }
  },
  methods: {
    closeModal() { 
      this.$emit('closeModal')
    }
  },
}
</script>