const url = process.env.VUE_APP_API_URL + "users/"

export default {
  namespaced: true,

  state: {
    records: [],
    isLoading: false,
    addLoading: false,
    addError: null,
    addErrors: [],
    updateLoading: false,
    updateError: null,
    updateErrors: [],
    resetPassLoading: false,
    resetPassError: null,
  },

  mutations: {
    setRecords (state, data) {
      state.records = data
    },
    setIsLoading (state, val) {
      state.isLoading = val
    },
    setAddLoading (state, val) {
      state.addLoading = val
    },
    setAddError (state, error) {
      state.addError = error
    },
    setAddErrors (state, errors) {
      state.addErrors = errors
    },
    setUpdateLoading (state, val) {
      state.updateLoading = val
    },
    setUpdateError (state, error) {
      state.updateError = error
    },
    setUpdateErrors (state, errors) {
      state.updateErrors = errors
    },
    setResetPassLoading (state, val) {
      state.resetPassLoading = val
    },
    setResetPassError (state, error) {
      state.resetPassError = error
    },
  },

  actions: {
    async getRecords({ commit, rootState }) {
      commit("setIsLoading", true)

      try {
        const response = await fetch(`${url}get`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${rootState.userToken}`,
          }
        });

        const res = await response.json()
        
        if (!response.ok) {
          throw { response, res }
        } else {
          commit("setRecords", res.records)
        }
      } catch(error) {
        const errorStatus = error.response.status
        
        if (errorStatus==401) {
          commit("setError", "Unauthorized", { root: true })
        } else {
          commit("setRefreshError", "Submitted invalid data", { root: true })
        }
      } finally {
        commit("setIsLoading", false)
      }
    },

    async addRecord({ commit, rootState }, data) {
      commit("setAddLoading", true)

      try {
        const response = await fetch(`${url}add`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${rootState.userToken}`,
          },
          body: JSON.stringify(data)
        });

        const res = await response.json()
        
        if (!response.ok) {
          throw { response, res }
        } else {
          commit("setAddErrors", []) 
          commit("setAddError", null) 
        }
      } catch(error) {
        const errorStatus = error.response.status
        
        if (errorStatus==422) {
          commit("setAddErrors", error.res.messages)
          commit("setAddError", null) 
        } else if (errorStatus==424) {
          commit("setAddErrors", [])
          commit("setAddError", error.res.error)
        } else if (errorStatus==401) {
          commit("setError", "Unauthorized", { root: true }) // force logout
        } else {
          commit("setPopUpError", "Something went wrong, please try again!", { root: true })
        }
      } finally {
        commit("setAddLoading", false)
      }
    },

    async updateRecord({ commit, rootState }, data) {
      commit("setUpdateLoading", true)

      try {
        const response = await fetch(`${url}update`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${rootState.userToken}`,
          },
          body: JSON.stringify(data)
        });

        const res = await response.json()
        
        if (!response.ok) {
          throw { response, res }
        } else {
          commit("setUpdateErrors", [])
          commit("setUpdateError", null) 
        }
      } catch(error) {
        const errorStatus = error.response.status
        
        if (errorStatus==422) {
          commit("setUpdateErrors", error.res.messages)
          commit("setUpdateError", null) 
        } else if (errorStatus==424) {
          commit("setUpdateErrors", [])
          commit("setUpdateError", error.res.error)
        } else if (errorStatus==401) {
          commit("setError", "Unauthorized", { root: true }) // force logout
        } else {
          commit("setPopUpError", "Something went wrong, please try again!", { root: true })
        }
      } finally {
        commit("setUpdateLoading", false)
      }
    },

    async resetPassword({ commit, rootState }, data) {
      commit("setResetPassLoading", true)

      try {
        const response = await fetch(`${url}reset_password`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${rootState.userToken}`,
          },
          body: JSON.stringify(data)
        });

        const res = await response.json()
        
        if (!response.ok) {
          throw { response, res }
        } else {
          commit("setResetPassError", null) 
        }
      } catch(error) {
        const errorStatus = error.response.status
        
        if (errorStatus==424) {
          commit("setResetPassError", error.res.error)
        } else if (errorStatus==401) {
          commit("setError", "Unauthorized", { root: true }) // force logout
        } else {
          commit("setPopUpError", "Something went wrong, please try again!", { root: true })
        }
      } finally {
        commit("setResetPassLoading", false)
      }
    },
  }
}