<template>
  <v-dialog
    v-model="dialog"
    width="1200"
  >
    <v-card>
      <v-card-title>
        <span class="text-h6 text-uppercase">Edit Personal Information</span>
        <v-spacer></v-spacer>
        <v-btn
          icon
          @click="closeModal"
        >
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-card-title>
      <v-divider></v-divider>
      <v-card-text class="pt-3 pb-6">
        <v-alert
          :value="!!editPersonalInfoError"
          dense
          outlined
          type="error"
          transition="scale-transition"
          icon="mdi-exclamation-thick"
          class="mb-3"
        >
          {{ editPersonalInfoError }}
        </v-alert>
        <v-form
          ref="form"
          v-model="valid"
          @submit.prevent="handleSubmit"
        >
          <div class="mb-2">
            <small>* indicates required field</small>
          </div>
          <v-row>
            <v-col
              cols="12"
              sm="6"
              md="3"
            >
              <v-text-field
                ref="surname"
                :rules="[rules.alphaSpace, rules.minimum(2), rules.maximum(30)]"
                v-model="surname"
                label="Surname"
                counter="30"
                maxlength="30"
                hide-details="auto"
              ></v-text-field>
            </v-col>
            <v-col
              cols="12"
              sm="6"
              md="3"
            >
              <v-text-field
                :rules="[rules.alphaSpace, rules.minimum(2), rules.maximum(30)]"
                v-model="given_name"
                label="Given Name"
                counter="30"
                maxlength="30"
                hide-details="auto"
              ></v-text-field>
            </v-col>
            <v-col
              cols="12"
              sm="6"
              md="3"
            >
              <v-text-field
                :rules="[rules.alphaSpace, rules.maximum(30)]"
                v-model="middle_name"
                label="Middle Name"
                counter="30"
                maxlength="30"
                hide-details="auto"
              ></v-text-field>
            </v-col>
            <v-col
              cols="12"
              sm="6"
              md="3"
            >
              <v-text-field
                :rules="[rules.alphaSpace, rules.minimum(2), rules.maximum(30)]"
                v-model="nickname"
                label="Nickname"
                counter="30"
                maxlength="30"
                hide-details="auto"
              ></v-text-field>
            </v-col>
            <v-col 
              cols="12"
              sm="6"
              md="9"
            >
              <v-text-field
                :rules="[rules.maximum(200)]"
                v-model="address"
                label="Address"
                counter="200"
                maxlength="200"
                hide-details="auto"
              ></v-text-field>
            </v-col>
            <v-col
              cols="12"
              sm="6"
              md="3"
            >
              <v-menu
                v-model="birthdate_menu"
                :close-on-content-click="false"
                transition="scale-transition"
                offset-y
                min-width="auto"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="birthdate"
                    label="Birthdate"
                    readonly
                    v-bind="attrs"
                    v-on="on"
                    @keyup="birthdate_menu=true"
                    @keydown="birthdate_menu=false"
                    hide-details="auto"
                  ></v-text-field>
                </template>
                <v-date-picker
                  v-model="birthdate"
                  @input="birthdate_menu = false"
                  :max="(new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10)"
                ></v-date-picker>
              </v-menu>
            </v-col>
            <v-col
              cols="12"
              sm="6"
              md="2"
            >
              <v-text-field
                v-model="getAge"
                label="Age"
                readonly
                hide-details="auto"
              ></v-text-field>
            </v-col>
            <v-col
              cols="12"
              sm="6"
              md="3"
            >
              <v-select
                v-model="gender"
                :items="gender_choices"
                label="Gender"
                hide-details="auto"
              ></v-select>
            </v-col>
            <v-col
              cols="12"
              sm="6"
              md="3"
            >
              <v-select
                v-model="civil_status"
                :items="civil_status_choices"
                label="Civil Status"
                hide-details="auto"
              ></v-select>
            </v-col>
            <v-col
              cols="12"
              sm="6"
              md="4"
            >
              <v-text-field
                :rules="[rules.validMobileNumber]"
                v-model="contact"
                label="Contact Number"
                hint="must be a valid mobile number, example: 09293158777"
                persistent-hint
                counter="11"
                maxlength="11"
                hide-details="auto"
              ></v-text-field>
            </v-col>
            <v-col
              cols="12"
              sm="6"
              md="3"
            >
              <v-text-field
                ref="email"
                :rules="[rules.required, rules.validEmail, emailExist]"
                v-model="email"
                label="Email Address*"
                hide-details="auto"
              ></v-text-field>
            </v-col>
            <v-col
              cols="12"
              sm="6"
              md="3"
            >
              <v-text-field
                ref="tin"
                :rules="[rules.numeric, rules.maximum(12)]"
                v-model="tin"
                label="TIN"
                counter="12"
                maxlength="12"
                hide-details="auto"
                type="text"
              ></v-text-field>
            </v-col>
          </v-row>
          <div class="mt-5">
            <v-btn
              :disabled="!valid"
              type="submit"
              color="blue darken-3 white--text"
              class="mr-2 mt-2"
              :loading="editPersonalInfoLoading"
              depressed
            >
              Save
            </v-btn>
            <v-btn
              @click="closeModal"
              color="secondary"
              class="mt-2"
              depressed
              outlined
            >
              Cancel
            </v-btn>
          </div>
        </v-form>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
import { mapState, mapActions } from "vuex"
import { isObjectEmpty } from '@/helpers/form'
import formRules from '@/helpers/formRules'

export default {
  props: {
    modalData: {
      type: Object,
      required: true
    }
  },
  data: () => ({
    valid: true,
    rules: formRules,
    surname: '',
    given_name: '',
    middle_name: '',
    nickname: '',
    address: '',
    birthdate: '',
    birthdate_menu: false,
    gender: '',
    gender_choices: [
      'Male',
      'Female'
    ],
    civil_status: '',
    civil_status_choices: [
      'Single',
      'Married',
      'Divorced',
      'Widowed'
    ],
    contact: '',
    email: '',
    emailExistText: '',
    tin: '',
  }),
  computed: {
    ...mapState("applicants", ["editPersonalInfoLoading", "editPersonalInfoError", "editPersonalInfoErrors"]),
    getAge: {
      get: function () {
        if (this.birthdate) {
          var today = new Date()
          var birthDate = new Date(this.birthdate)
          var age = today.getFullYear() - birthDate.getFullYear()
          var m = today.getMonth() - birthDate.getMonth()
          if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate())) {
            age--
          }
          return age
        }
        return 0
      },
      // setter
      set: function () {
        return 0
      }
    },
    dialog: {
      get: function () {
        return this.modalData.dialog
      },
      // setter
      set: function () {
        this.closeModal()
      }
    },
  },
  mounted() {
    this.$refs.surname.focus()
    this.surname = this.modalData.surname
    this.given_name = this.modalData.given_name
    this.middle_name = this.modalData.middle_name
    this.nickname = this.modalData.nickname
    this.address = this.modalData.address
    this.birthdate = this.modalData.birthdate
    this.gender = this.modalData.gender
    this.civil_status = this.modalData.civil_status
    this.contact = this.modalData.contact
    this.email = this.modalData.email
    this.tin = this.modalData.tin
  },
  methods: {
    ...mapActions("applicants", ["editPersonalInfo"]),
    emailExist(val) {
      if (val == this.emailExistText) {
        return `"${val}" already exist`
      } else {
        return true
      }
    },
    async handleSubmit () {
      if (!this.editPersonalInfoLoading) {
        const editData = {
          applicant_id: this.modalData.id, 
          surname: this.surname, 
          given_name: this.given_name, 
          middle_name: this.middle_name, 
          nickname: this.nickname, 
          address: this.address,
          birthdate: this.birthdate,
          gender: this.gender,
          civil_status: this.civil_status,
          contact: this.contact,
          email: this.email,
          tin: this.tin
        }

        const res = await this.editPersonalInfo(editData)

        if (!this.editPersonalInfoError && isObjectEmpty(this.editPersonalInfoErrors)) {
          this.$emit('editedPersonalInfo', res)
          this.closeModal()
        } else {
          if (!isObjectEmpty(this.editPersonalInfoErrors)) {
            
            this.emailExistText = this.email
            this.$refs['email'].focus()
            
            this.$refs.form.validate()
          } else {
            this.$refs.form.resetValidation()
          }
        }
      }
    },
    closeModal() { 
      this.$emit('closeModal')
    }
  },
}
</script>