<template>
  <v-dialog
    v-model="dialog"
    width="600"
  >
    <v-card
      :loading="statusLoading"
    >
      <v-card-title>
        <span class="text-h6">Update Status</span>
      </v-card-title>
      <v-divider></v-divider>
      <v-card-text class="pt-3 pb-6">
        <v-alert
          :value="!!updateStatusError"
          dense
          outlined
          type="error"
          transition="scale-transition"
          icon="mdi-exclamation-thick"
          class="mb-3"
        >
          {{ updateStatusError }}
        </v-alert>
        <v-form
          :disabled="statusLoading"
          ref="form"
          v-model="valid"
          @submit.prevent="handleSubmit"
        >
          <div class="mb-2">
            <small>* indicates required field</small>
          </div>
          <v-row>
            <v-col
              cols="12"
            >
              <v-autocomplete
                ref="applicant_status"
                :rules="[rules.required]"
                v-model="applicant_status"
                :items="applicantStatuses"
                label="Applicant Status*"
                hide-details="auto"
                @change="status_tag = ''"
              ></v-autocomplete>
            </v-col>
            <v-col
              cols="12"
            >
              <v-autocomplete
                ref="status_tag"
                v-model="status_tag"
                :items="status_tags"
                label="Status Tag"
                hide-details="auto"
              ></v-autocomplete>
            </v-col>
            <v-col
              cols="12"
            >
              <v-textarea
                ref="remarks"
                :rules="[rules.maximum(300)]"
                v-model="remarks"
                label="Remarks"
                hide-details="auto"
                no-resize
                rows="3"
                counter="300"
                maxlength="300"
              >
              </v-textarea>
            </v-col>
          </v-row>
          <div class="mt-5">
            <v-btn
              :disabled="!valid"
              type="submit"
              color="blue darken-3 white--text"
              class="mr-2 mt-2"
              :loading="updateStatusLoading"
              depressed
            >
              Save
            </v-btn>
            <v-btn
              @click="closeModal"
              color="secondary"
              class="mt-2"
              depressed
              outlined
            >
              Cancel
            </v-btn>
          </div>
        </v-form>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
import { mapState, mapActions } from "vuex"
import applicant_statuses from '@/data/applicant_statuses.json'
import applicant_status_tags from '@/data/applicant_status_tags.json'
import formRules from '@/helpers/formRules'
import { isObjectEmpty } from '@/helpers/form'

export default {
  props: {
    modalData: {
      type: Object,
      required: true
    }
  },
  data: () => ({
    valid: true,
    rules: formRules,
    applicant_status: '',
    applicantStatuses: applicant_statuses,
    status_tag: '',
    remarks: '',
    last_status_id: ''
  }),
  created() {
    if (!this.statusLoading) {
      this.$store.commit("applicants/setStatusData", [])
      this.$store.commit("applicants/setUpdateStatusError", null)
      this.$store.commit("applicants/setUpdateStatusErrors", [])
      this.getStatusData({ applicant_id: this.modalData.id })
    }
  },
  computed: {
    ...mapState("applicants", ["statusLoading", "statusError", "statusData", "updateStatusLoading", "updateStatusError", "updateStatusErrors"]),
    status_tags() {
      return applicant_status_tags.filter((tag) => {
        return tag.applicant_status_id == this.applicant_status
      })
    },
    dialog: {
      get: function () {
        return this.modalData.dialog
      },
      // setter
      set: function () {
        this.closeModal()
      }
    },
    selectedStatus() {
      return this.applicantStatuses.filter((status) => {
        return status.value == this.applicant_status
      })
    }
  },
  methods: {
    ...mapActions("applicants", ["getStatusData", "updateStatus"]),
    async handleSubmit () {
      if (!this.statusLoading && !this.updateStatusLoading) {
        const updateData = { 
          applicant_id: this.modalData.id, 
          applicant_status_id: this.applicant_status,
          applicant_status_tag_id: this.status_tag,
          status_remarks: this.remarks,
          last_status_id: this.last_status_id
        }

        await this.updateStatus(updateData)

        if (!this.updateStatusError && isObjectEmpty(this.updateStatusErrors)) {
          // this.modalData.status = 'Test'

          // Update status in list
          this.$emit('success', this.selectedStatus[0].value, this.selectedStatus[0].text)

          this.closeModal()
        } else {
          // error occurred
        }
      }
    },
    closeModal() { 
      this.$emit('closeModal')
    }
  },

  watch: {
    statusData(newVal/*, oldVal*/) {
      // Set the value of after fetching the data from db
      this.applicant_status = parseInt(newVal.applicant_status_id)
      this.status_tag = (newVal.applicant_status_tag_id) ? parseInt(newVal.applicant_status_tag_id) : 0
      this.remarks = newVal.status_remarks
      this.last_status_id = newVal.last_status_id
    }
  },
}
</script>