<template>
  <v-dialog
    v-model="dialog"
    width="600"
  >
    <v-card>
      <v-card-title>
        <span class="text-h6">Search Options</span>
      </v-card-title>
      <v-divider></v-divider>
      <v-card-text class="pt-3 pb-6">
        <!-- <p>{{ modalData }}</p>
        <p>{{ createdAt }}</p>
        <p>{{ dateOfApplication }}</p> -->
        <!-- 
          1. dapat matrigger yung date pag nag focus
          2. dapat removeable - Done
         -->
        <v-form
          ref="form"
          @submit.prevent="handleSubmit"
        >
          <v-row>
            <v-col cols="12" md="6">
              <v-autocomplete
                ref="status"
                v-model="status"
                :items="applicantStatuses"
                label="Status"
                hide-details="auto"
                clearable
              ></v-autocomplete>
            </v-col>
            <v-col cols="12" md="6">
              <v-menu
                ref="menu1"
                v-model="menu1"
                :close-on-content-click="false"
                :return-value.sync="createdAt"
                transition="scale-transition"
                offset-y
                min-width="auto"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="createdAtText"
                    label="Created At"
                    readonly
                    v-bind="attrs"
                    v-on="on"
                    hide-details="auto"
                    clearable
                    @keyup="menu1=true"
                    @keydown="menu1=false"
                    @click:clear="createdAt = []"
                  ></v-text-field>
                </template>
                <v-date-picker
                  v-model="createdAt"
                  no-title
                  scrollable
                  range
                >
                  <v-spacer></v-spacer>
                  <v-btn
                    text
                    color="blue darken-3"
                    @click="menu1 = false"
                  >
                    Cancel
                  </v-btn>
                  <v-btn
                    text
                    color="blue darken-3"
                    @click="$refs.menu1.save(createdAt)"
                  >
                    OK
                  </v-btn>
                </v-date-picker>
              </v-menu>
            </v-col>
            <v-col cols="12" md="6">
              <v-menu
                ref="menu2"
                v-model="menu2"
                :close-on-content-click="false"
                :return-value.sync="dateOfApplication"
                transition="scale-transition"
                offset-y
                min-width="auto"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="dateOfApplicationText"
                    label="Date of Application"
                    readonly
                    v-bind="attrs"
                    v-on="on"
                    hide-details="auto"
                    clearable
                    @keyup="menu2=true"
                    @keydown="menu2=false"
                    @click:clear="dateOfApplication = []"
                  ></v-text-field>
                </template>
                <v-date-picker
                  v-model="dateOfApplication"
                  no-title
                  scrollable
                  range
                >
                  <v-spacer></v-spacer>
                  <v-btn
                    text
                    color="blue darken-3"
                    @click="menu2 = false"
                  >
                    Cancel
                  </v-btn>
                  <v-btn
                    text
                    color="blue darken-3"
                    @click="$refs.menu2.save(dateOfApplication)"
                  >
                    OK
                  </v-btn>
                </v-date-picker>
              </v-menu>
            </v-col>
            <v-col cols="12" md="6">
              <v-menu
                ref="menu3"
                v-model="menu3"
                :close-on-content-click="false"
                :return-value.sync="statusUpdatedAt"
                transition="scale-transition"
                offset-y
                min-width="auto"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="statusUpdatedAtText"
                    label="Status Updated At"
                    readonly
                    v-bind="attrs"
                    v-on="on"
                    hide-details="auto"
                    clearable
                    @keyup="menu3=true"
                    @keydown="menu3=false"
                    @click:clear="statusUpdatedAt = []"
                  ></v-text-field>
                </template>
                <v-date-picker
                  v-model="statusUpdatedAt"
                  no-title
                  scrollable
                  range
                >
                  <v-spacer></v-spacer>
                  <v-btn
                    text
                    color="blue darken-3"
                    @click="menu3 = false"
                  >
                    Cancel
                  </v-btn>
                  <v-btn
                    text
                    color="blue darken-3"
                    @click="$refs.menu3.save(statusUpdatedAt)"
                  >
                    OK
                  </v-btn>
                </v-date-picker>
              </v-menu>
            </v-col>
            <v-col cols="12" md="6">
              <v-autocomplete
                ref="sales_group"
                v-model="sales_group"
                :items="salesGroups"
                label="Sales Group"
                hide-details="auto"
                clearable
              ></v-autocomplete>
            </v-col>
            <v-col cols="12" md="6">
              <v-autocomplete
                ref="position"
                v-model="position"
                :items="positions"
                label="Position"
                hide-details="auto"
                clearable
              ></v-autocomplete>
            </v-col>
          </v-row>
          <div class="mt-4">
            <v-btn
              type="submit"
              color="blue darken-3 white--text"
              class="mr-2 mt-2"
              depressed
            >
              Search
            </v-btn>
            <v-btn
              @click="closeModal"
              color="secondary"
              class="mt-2"
              depressed
              outlined
            >
              Cancel
            </v-btn>
          </div>
        </v-form>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
import { mapState } from "vuex"
// import salesGroups from '@/data/salesGroups.json'
import positions from '@/data/positions.json'
import formRules from '@/helpers/formRules'
import applicant_statuses from '@/data/applicant_statuses.json'

export default {
  props: {
    modalData: {
      type: Object,
      required: true
    }
  },
  data: () => ({
    rules: formRules,
    applicantStatuses: applicant_statuses,
    status: '',
    menu1: false,
    createdAt: [],
    menu2: false,
    dateOfApplication: [],
    menu3: false,
    statusUpdatedAt: [],
    sales_group: '',
    // sales_groups: salesGroups,
    position: '',
    positions: positions
  }),
  computed: {
    ...mapState("applicants", ["salesGroups"]),
    createdAtText: {
      get: function () {
        return this.createdAt.join(' ~ ')
      },
      // setter
      set: function () {
        return ''
      }
    },
    dateOfApplicationText: {
      get: function () {
        return this.dateOfApplication.join(' ~ ')
      },
      // setter
      set: function () {
        return ''
      }
    },
    statusUpdatedAtText: {
      get: function () {
        return this.statusUpdatedAt.join(' ~ ')
      },
      // setter
      set: function () {
        return ''
      }
    },
    dialog: {
      get: function () {
        return this.modalData.dialog
      },
      // setter
      set: function () {
        this.closeModal()
      }
    },
  },
  mounted() {
    this.$refs.status.focus()
    this.status = this.modalData.statusDefault
    this.createdAt = this.modalData.createdAtDefault
    this.dateOfApplication = this.modalData.dateOfApplicationDefault
    this.statusUpdatedAt = this.modalData.statusUpdatedAtDefault
    this.sales_group = this.modalData.salesGroupDefault
    this.position = this.modalData.positionDefault
  },
  methods: {
    handleSubmit () {
      const searchOptionsData = {
        status: this.status,
        created_at: this.createdAt,
        date_of_application: this.dateOfApplication,
        status_updated_at: this.statusUpdatedAt,
        sales_group: this.sales_group,
        position: this.position
      }

      // console.log(searchOptionsData)

      this.$emit('searchOptionsSubmit', searchOptionsData)
    },

    closeModal() { 
      this.$emit('closeModal')
    }
  },
}
</script>