<template>
  <v-dialog
    v-model="dialog"
    width="1200"
  >
    <v-card>
      <v-card-title>
        <span class="text-h6">Add Applicant</span>
      </v-card-title>
      <v-divider></v-divider>
      <v-card-text class="pt-3 pb-6">
        <v-alert
          :value="!!addError"
          dense
          outlined
          type="error"
          transition="scale-transition"
          icon="mdi-exclamation-thick"
          class="mb-3"
        >
          {{ addError }}
        </v-alert>
        <v-form
          ref="form"
          v-model="valid"
          @submit.prevent="handleSubmit"
        >
          <div class="mb-2">
            <small>* indicates required field</small>
          </div>
          <v-row>
            <v-col
              cols="12"
              sm="6"
              md="3"
            >
              <v-text-field
                ref="surname"
                :rules="[rules.alphaSpace, rules.minimum(2), rules.maximum(30)]"
                v-model="surname"
                label="Surname"
                counter="30"
                maxlength="30"
                hide-details="auto"
              ></v-text-field>
            </v-col>
            <v-col
              cols="12"
              sm="6"
              md="3"
            >
              <v-text-field
                ref="given_name"
                :rules="[rules.alphaSpace, rules.minimum(2), rules.maximum(30)]"
                v-model="given_name"
                label="Given Name"
                counter="30"
                maxlength="30"
                hide-details="auto"
              ></v-text-field>
            </v-col>
            <v-col
              cols="12"
              sm="6"
              md="3"
            >
              <v-text-field
                ref="middle_name"
                :rules="[rules.alphaSpace, rules.maximum(30)]"
                v-model="middle_name"
                label="Middle Name"
                counter="30"
                maxlength="30"
                hide-details="auto"
              ></v-text-field>
            </v-col>
            <v-col
              cols="12"
              sm="6"
              md="3"
            >
              <v-text-field
                ref="nickname"
                :rules="[rules.alphaSpace, rules.minimum(2), rules.maximum(30)]"
                v-model="nickname"
                label="Nickname"
                counter="30"
                maxlength="30"
                hide-details="auto"
              ></v-text-field>
            </v-col>
            <v-col cols="12" md="6">
              <v-text-field
                ref="email"
                :rules="[rules.required, rules.validEmail, emailExist]"
                v-model="email"
                label="Email*"
                hide-details="auto"
                required
              ></v-text-field>
            </v-col>
            <v-col cols="12" md="6">
              <v-text-field
                ref="contact"
                :rules="[rules.validMobileNumber]"
                v-model="contact"
                label="Contact"
                hint="must be a valid mobile number, example: 09293158777"
                persistent-hint
                counter="11"
                maxlength="11"
                hide-details="auto"
              ></v-text-field>
            </v-col>
            <v-col cols="12" md="4">
              <v-autocomplete
                ref="sales_group"
                :rules="[rules.required]"
                v-model="sales_group"
                :items="salesGroups"
                label="Sales Group*"
                hide-details="auto"
              ></v-autocomplete>
            </v-col>
            <v-col cols="12" md="4">
              <v-autocomplete
                ref="position"
                :rules="[rules.required]"
                v-model="position"
                :items="positions"
                label="Position*"
                hide-details="auto"
                return-object
              ></v-autocomplete>
            </v-col>
            <v-col cols="12" md="4">
              <v-menu
                v-model="date_of_application_menu"
                :close-on-content-click="false"
                transition="scale-transition"
                offset-y
                min-width="auto"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    ref="date_of_application"
                    v-model="date_of_application"
                    label="Date of Application"
                    readonly
                    v-bind="attrs"
                    v-on="on"
                    @keyup="date_of_application_menu=true"
                    @keydown="date_of_application_menu=false"
                    hide-details="auto"
                    clearable
                  ></v-text-field>
                </template>
                <v-date-picker
                  v-model="date_of_application"
                  @input="date_of_application_menu = false"
                ></v-date-picker>
              </v-menu>
            </v-col>
            <v-col cols="12" md="5">
              <v-autocomplete
                ref="test"
                :rules="[rules.required]"
                v-model="selectedTests"
                :items="tests"
                label="Tests*"
                hide-details="auto"
                chips
                multiple
              ></v-autocomplete>
            </v-col>
          </v-row>
          <v-checkbox
            v-model="send_link"
            label="Send Application Link"
          ></v-checkbox>
          <div>
            <v-btn
              :disabled="!valid"
              type="submit"
              color="blue darken-3 white--text"
              class="mr-2 mt-2"
              :loading="addLoading"
              depressed
            >
              Add
            </v-btn>
            <v-btn
              @click="closeModal"
              color="secondary"
              class="mt-2"
              depressed
              outlined
            >
              Cancel
            </v-btn>
          </div>
        </v-form>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
import { mapState, mapActions } from "vuex"
import { isObjectEmpty } from '@/helpers/form'
// import salesGroups from '@/data/salesGroups.json'
import positions from '@/data/positions.json'
import tests from '@/data/tests.json'
import formRules from '@/helpers/formRules'

export default {
  props: {
    modalData: {
      type: Object,
      required: true
    }
  },
  data: () => ({
    valid: true,
    rules: formRules,
    surname: '',
    given_name: '',
    middle_name: '',
    nickname: '',
    email: '',
    emailExistText: '',
    contact: '',
    send_link: true,
    sales_group: '',
    // sales_groups: salesGroups,
    position: '',
    positions: positions,
    date_of_application: '',
    date_of_application_menu: false,
    tests: tests,
    selectedTests: []
  }),
  computed: {
    ...mapState("applicants", ["addLoading", "addError", "addErrors", "salesGroups"]),
    dialog: {
      get: function () {
        return this.modalData.dialog
      },
      // setter
      set: function () {
        this.closeModal()
      }
    }
  },
  mounted() {
    this.$refs.surname.focus()
  },
  methods: {
    ...mapActions("applicants", ["addApplicant"]),
    emailExist(val) {
      if (val == this.emailExistText) {
        return `"${val}" already exist`
      } else {
        return true
      }
    },

    async handleSubmit () {
      if (!this.addLoading) {
        await this.addApplicant({ surname: this.surname, given_name: this.given_name, middle_name: this.middle_name, nickname: this.nickname, email: this.email, contact: this.contact, sales_group: this.sales_group, position: this.position.value, date_of_application: this.date_of_application, tests: this.selectedTests, send_link: this.send_link })

        if (!this.addError && isObjectEmpty(this.addErrors)) {
          // reset form
          this.$refs.form.reset()
          // this.$refs.surname.focus()
          this.$emit('refresh')
          
          this.closeModal()
        } else {
          if (!isObjectEmpty(this.addErrors)) {
            // display errors
            // Expected error email

            // Object.entries(this.addErrors).forEach(([key, value]) => {
            //   if (value) {
            //     if (key=='email') {
            this.emailExistText = this.email
            this.$refs['email'].focus()
            //     }
            //   }
            // })

            this.$refs.form.validate()

            // focus in first error
            // const focusError = errorFocus(this.addErrors)
            // this.$refs[focusError].focus()
          } else {
            this.$refs.form.resetValidation()
          }
        }
      }
    },

    closeModal() { 
      this.$emit('closeModal')
    }
  },
  watch: {
    position(val) {
      this.selectedTests = val.tests
    }
  }
  /* watch: {
    dialog(visible) {
      if (visible) {
        setTimeout(() => {
          this.$refs.surname.focus()
        }, 200);
      } else {
        // remove error
        this.$store.commit("applicants/setAddError", null)

        // reset validation when form closed
        // this.$refs.form.resetValidation()
      }
    }
  }, */
}
</script>