const url = process.env.VUE_APP_API_URL + "tests"

export default {
  namespaced: true,

  state: {
    records: [],
    recordsLoading: false,
    createLoading: false,
    createError: null,
    show: [], // Show details
    showLoading: false,
  },

  mutations: {
    setRecords (state, data) {
      state.records = data
    },
    setRecordsLoading (state, val) {
      state.recordsLoading = val
    },
    setCreateLoading (state, val) {
      state.createLoading = val
    },
    setCreateError (state, error) {
      state.createError = error
    },
    setShow (state, data) {
      state.show = data
    },
    setShowLoading (state, val) {
      state.showLoading = val
    },
  },

  actions: {
    async getRecords({ commit, rootState }) {
      commit("setRecordsLoading", true)

      try {
        const response = await fetch(`${url}`, {
          method: 'GET',
          headers: {
            'Authorization': `Bearer ${rootState.userToken}`,
          }
        });

        const res = await response.json()
        
        if (!response.ok) {
          throw { response, res }
        } else {
          commit("setRecords", res.records)
        }
      } catch(error) {
        const errorStatus = error.response.status
        
        if (errorStatus==401) {
          commit("setError", "Unauthorized", { root: true })
        } else {
          commit("setRefreshError", "Submitted invalid data", { root: true })
        }
      } finally {
        commit("setRecordsLoading", false)
      }
    },

    async createRecord({ commit, rootState }, data) {
      commit("setCreateLoading", true)

      try {
        const response = await fetch(`${url}`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${rootState.userToken}`,
          },
          body: JSON.stringify(data)
        });

        const res = await response.json()
        
        if (!response.ok) {
          throw { response, res }
        } else {
          commit("setCreateError", null)
          return res
        }
      } catch(error) {
        const errorStatus = error.response.status
        
        if (errorStatus==424) {
          commit("setCreateError", error.res.error)
        } else if (errorStatus==401) {
          commit("setError", "Unauthorized", { root: true }) // force logout
        } else {
          commit("setPopUpError", "Something went wrong, please try again!", { root: true })
        }
      } finally {
        commit("setCreateLoading", false)
      }
    },

    async showDetails({ commit, rootState }) {
      commit("setRecordsLoading", true)

      try {
        const response = await fetch(`${url}show`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${rootState.userToken}`,
          }
        });

        const res = await response.json()
        
        if (!response.ok) {
          throw { response, res }
        } else {
          commit("setRecords", res.records)
        }
      } catch(error) {
        const errorStatus = error.response.status
        
        if (errorStatus==401) {
          commit("setError", "Unauthorized", { root: true })
        } else {
          commit("setRefreshError", "Submitted invalid data", { root: true })
        }
      } finally {
        commit("setRecordsLoading", false)
      }
    },

    /* async updateQuestion({ commit, rootState }, data) {
      commit("setUpdateLoading", true)

      try {
        const response = await fetch(`${url}update`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${rootState.userToken}`,
          },
          body: JSON.stringify(data)
        });

        const res = await response.json()
        
        if (!response.ok) {
          throw { response, res }
        } else {
          commit("setUpdateError", null) 
        }
      } catch(error) {
        const errorStatus = error.response.status
        
        if (errorStatus==424) {
          commit("setAddError", error.res.error)
        } else if (errorStatus==401) {
          commit("setError", "Unauthorized", { root: true }) // force logout
        } else {
          commit("setPopUpError", "Something went wrong, please try again!", { root: true })
        }
      } finally {
        commit("setUpdateLoading", false)
      }
    }, */
  }
}