<template>
  <v-container class="mt-3">
    <v-row no-gutters justify="center">
      <v-col sm="9" md="6" lg="5" xl="4">
        <v-card
          elevation="2"
          class="pa-6"
        >
          <v-card-title class="mb-3">Sign In Form</v-card-title>
          <v-card-subtitle>Start creating the best possible user experience for you customers.</v-card-subtitle>
          <v-card-text class="mt-4">
            <v-alert
              :value="!!error"
              dense
              outlined
              type="error"
              transition="scale-transition"
              icon="mdi-exclamation-thick"
              class="mb-8"
            >
              {{ error }}
            </v-alert>
            <v-form
              ref="form"
              v-model="valid"
              @submit.prevent="handleSubmit"
            >
              <v-text-field
                ref="email"
                v-model="email"
                :rules="emailRules"
                label="E-mail"
                prepend-icon="mdi-email"
                required
                class="mb-4"
              ></v-text-field>
              <v-text-field
                v-model="password"
                :append-icon="showPassword? 'mdi-eye' : 'mdi-eye-off'"
                :rules="passwordRules"
                :type="showPassword ? 'text' : 'password'"
                label="Password"
                prepend-icon="mdi-lock"
                @click:append="showPassword = !showPassword"
              ></v-text-field>
              <div class="text-right mt-2">
                <a href="#" class="text-decoration-none subtitle-1">Forgot Password?</a>
              </div>
              <v-btn
                :disabled="!!error ? isLoading : !valid"
                class="mt-7 py-5"
                block
                color="primary"
                elevation="2"
                type="submit"
                :loading="isLoading"
              >
              Sign In
              </v-btn>
            </v-form>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  import { mapState, mapActions } from "vuex"
  
  export default {
    name: 'Home',
    data: () => ({
      valid: false,
      email: '',
      emailRules: [
        v => !!v || 'Please enter your e-mail.',
        v => /.+@.+\..+/.test(v) || 'E-mail must be valid'
      ],
      password: '',
      showPassword: false,
      passwordRules: [
        v => !!v || 'Please enter your password.'
      ]
    }),
    created() {
      this.$store.commit("resetStates") // reset main states
    },
    mounted() {
      this.$refs.email.focus()
    },
    computed: {
      ...mapState("login", ["error", "isLoading"])
    },
    methods: {
      ...mapActions("login", ["sendLoginRequest"]),
      async handleSubmit () {
        if (this.$refs.form.validate()) {
          this.$store.commit("login/setError", null)

          this.valid = false

          await this.sendLoginRequest({ email: this.email, password: this.password })

          if (!this.error) {
            this.$router.push({ name: "Dashboard" })
          } else {
            this.valid = true
          }
        }
      }
    }
  }
</script>
