<template>
  <div class="d-inline-block">
    <v-tooltip right>
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          v-bind="attrs"
          v-on="on"
          @click="handleClick"
          icon
          small
        >
          <v-icon
            size="large"
            color="blue darken-3"
          >
            mdi-account-edit-outline
          </v-icon>
        </v-btn>
      </template>
      <span>Update Status</span>
    </v-tooltip>
    <UpdateStatusModal v-if="updateStatusModal" :modalData="updateStatusModalData" @success="success"  @closeModal="updateStatusModal = false" />
  </div>
</template>

<script>
import UpdateStatusModal from "@/components/applicants/UpdateStatusModal"

export default {
  components: { UpdateStatusModal },
  props: {
    item: {
      type: Object,
      required: true
    }
  },
  data: () => ({
    updateStatusModal: false,
    updateStatusModalData: [],
  }),
  methods: {
    handleClick() {
      this.updateStatusModal = true
      this.updateStatusModalData = { ...this.item, dialog: this.updateStatusModal }
    },
    success(id, text) {
      this.$emit('updatedStatus', this.item.id, id, text)
    }
  }
}
</script>