<template>
  <v-app dark> 
    <Navbar v-if="isAuthenticated && currentRouteName != 'Applicant'" />
    <v-main>  
      <router-view />
    </v-main>
  </v-app>
</template>

<script>
import Navbar from '@/components/Navbar'
export default {
  name: 'App',
  components: { Navbar },
  computed: { 
    isAuthenticated() {
      return this.$store.getters.isAuthenticated
    },
    currentRouteName() {
      return this.$route.name;
    }
  },
};
</script>