<template>
  <div>
    <v-navigation-drawer v-model="drawer" app>
      <v-list class="py-1">
        <v-list-item class="ma-0">
          <v-list-item-avatar>
            <v-img src="/logo.png"></v-img>
          </v-list-item-avatar>

          <v-list-item-content>
            <v-list-item-title class="text-h5">Online Exam</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>

      <v-divider></v-divider>

      <v-list class="pt-0">
        <v-list-item
          v-for="link in userLinks"
          :key="link.title"
          link
          router
          :to="{ name: link.route }"
        >
          <v-list-item-icon>
            <v-icon>{{ link.icon }}</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>{{ link.text }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>

    <v-app-bar elevation="2" class="blue darken-3" dark app>
      <v-app-bar-nav-icon @click="drawer = !drawer"></v-app-bar-nav-icon>
      <v-toolbar-title>{{ $route.meta.title || $route.name }}</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-menu
        offset-y
        transition="slide-y-transition"
        bottom
      >
        <template v-slot:activator="{ on, attrs }">
          <v-btn
            v-bind="attrs"
            v-on="on"
            text
            small
          >
            {{ userData.first_name }}
            <v-icon size="22" right>mdi-chevron-down</v-icon>
          </v-btn>
        </template>
        <v-list>
          <v-list-item
            link
            @click="changePassword"
          >
            <v-icon class="mr-3">mdi-lock-reset</v-icon>
            <v-list-item-title>Change Password</v-list-item-title>
          </v-list-item>
          <v-list-item
            link
            @click="handleClick"
          >
            <v-icon class="mr-3">mdi-logout-variant</v-icon>
            <v-list-item-title>Logout</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
    </v-app-bar>
    <ChangePasswordModal
      v-if="changePasswordModal"
      :modalData="changePasswordModalData"
      @closeModal="closeChangePassword"
      @success="handleClick"
    />
    <v-dialog
      v-model="unauthorizedModal"
      persistent
      min-width="300"
    >
      <v-card>
        <v-card-title class="text-h5">
          Error
        </v-card-title>
        <v-card-text>You are not authorized, you will be logged out.</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="handleClick"
          >
            Okay
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="refreshErrorModal"
      persistent
      min-width="300"
    >
      <v-card>
        <v-card-title class="text-h5">
          Error
        </v-card-title>
        <v-card-text>{{ refreshError }}</v-card-text>
        <v-card-text>This page will be refreshed.</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="refreshPage"
          >
            Okay
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="popUpErrorModal"
      persistent
      min-width="300"
    >
      <v-card>
        <v-card-title class="text-h5">
          Error
        </v-card-title>
        <v-card-text>{{ popUpError }}</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="closePopUpErrorModal"
          >
            Okay
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { mapGetters, mapState, mapActions } from "vuex";
import ChangePasswordModal from "@/components/ChangePasswordModal";

export default {
  components: { ChangePasswordModal },
  data: () => ({
    menu: false,
    drawer: null,
    links: [
      { text: "Dashboard", icon: "mdi-view-dashboard", route: "Dashboard" },
      { text: "Applicants", icon: "mdi-account-tie", route: "Applicants" },
      /* {
        text: "Tests",
        icon: "mdi-text-box-multiple-outline",
        route: "Tests",
      },
      {
        text: "Questions",
        icon: "mdi-chat-question-outline",
        route: "Questions",
      }, */
      { text: "Users", icon: "mdi-account-group", route: "Users" },
    ],
    // changePassActive: null, // to remove active in list item
    changePasswordModal: false,
    changePasswordModalData: [],
    unauthorizedModal: false,
    refreshErrorModal: false,
    popUpErrorModal: false
  }),
  computed: {
    ...mapGetters(["userRole"]),
    ...mapState(["userData", "error", "refreshError", "popUpError"]), // If show error via dialog and force logout
    ...mapState("logout", ["isLoading"]),
    userLinks() {
      if (this.userRole=='Admin') {
        // return [ ...this.links, { text: "Settings", icon: "mdi-cog-outline", route: "Settings" } ]
        return [ ...this.links ]
      } else {
        return [ ...this.links ]
      }
    }
  },
  methods: {
    ...mapActions("logout", ["sendLogoutRequest"]),
    changePassword() {
      this.menu = false;
      this.changePasswordModal = true;
      this.changePasswordModalData = { dialog: this.changePasswordModal };
    },
    async handleClick() {
      if (!this.isLoading) {
        await this.sendLogoutRequest();

        // if (!this.error) {
          this.$router.push({ name: "Home" });
        // }
      }
    },
    closeChangePassword() {
      // this.changePassActive = null;
      this.changePasswordModal = false;
    },
    closePopUpErrorModal() {
      this.popUpErrorModal = false

      this.$store.commit("setPopUpError", null)
    },
    refreshPage() {
      location.reload()
    }
  },
  watch: {
    error(val) {
      if (val) {
        this.unauthorizedModal = true
      }
    },
    refreshError(val) {
      if (val) {
        this.refreshErrorModal = true
      }
    },
    popUpError(val) {
      if (val) {
        this.popUpErrorModal = true
      }
    },
  }
};
</script>
