<template>
  <v-tooltip right>
    <template v-slot:activator="{ on, attrs }">
      <v-btn
        v-bind="attrs"
        v-on="on"
        @click="send"
        icon
        small
      >
        <v-progress-circular 
          v-if="loading"
          width="3"
          color="grey lighten-1"
          indeterminate
          size="18"
        ></v-progress-circular>
        <div v-else>
          <v-icon v-if="success"
            size="large"
            color="success"
          >
            mdi-email-fast-outline
          </v-icon>
          <v-icon v-else
            size="large"
            color="blue darken-3"
          >
            mdi-email-fast-outline
          </v-icon>
        </div>
      </v-btn>
    </template>
    <span>Send Application Link</span>
  </v-tooltip>
</template>

<script>
import { mapActions } from "vuex"

export default {
  props: {
    item: {
      type: Object,
      required: true
    }
  },
  data: () => ({
    loading: false,
    success: false
  }),
  methods: {
    ...mapActions("applicants", ["doSenDLink"]),
    async send() {
      if (!this.loading) {
        this.loading = true
    
        await this.doSenDLink([{ id: this.item.id }])

        this.loading = false

        this.success = true
        setTimeout(() => {
          this.success = false
        }, 2000)
      }
    },
  }
}
</script>