<template>
  <v-tooltip right>
    <template v-slot:activator="{ on, attrs }">
      <v-btn
        v-bind="attrs"
        v-on="on"
        @click="generate"
        icon
        small
      >
        <v-progress-circular 
          v-if="loading"
          width="3"
          color="grey lighten-1"
          indeterminate
          size="18"
        ></v-progress-circular>
        <v-icon v-else
          size="small"
          color="grey darken-2"
        >
          mdi-application-cog-outline
        </v-icon>
      </v-btn>
    </template>
    <span>Generate Score</span>
  </v-tooltip>
</template>

<script>
import { mapActions } from "vuex"

export default {
  props: {
    item: {
      type: Object,
      required: true
    }
  },
  data: () => ({
    loading: false
  }),
  methods: {
    ...mapActions("applicants", ["generateTestScore"]),
    async generate() {
      if (!this.loading) {
        this.loading = true

        const res = await this.generateTestScore({ ...this.item })

        this.loading = false

        this.$emit('updateTest', this.item.test_id, res.result, res.correct_answer, res.wrong_answer)
      }
    },
  }
}
</script>