import Vue from 'vue'
import Vuex from 'vuex'
import jwtdecode from '../helpers/jwtdecode'
import login from './login'
import logout from './logout'
import dashboard from './dashboard'
import applicants from './applicants'
import applicant from './applicant'
import questions from './questions'
import tests from './tests'
import users from './users'
import account from './account'
import settings from './settings'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    userData: jwtdecode(localStorage.getItem('userToken')) || null,
    userToken: localStorage.getItem('userToken') || null,
    error: null, // This error is for unauthorized(no token, wrong token, account not found, account is not logged in), automatic logout ito
    refreshError: null,
    popUpError: null, // error na popup usually dito babagsak yung internal server error, pero gagawin lang natin na something went wrong na error
  },
  getters: {
    isAuthenticated: state => !!state.userData,
    userRole: state => state.userData ? state.userData.role : null
  },
  mutations: {
    setUserData (state, data) {
      state.userData = data
    },
    setUserToken (state, data) {
      state.userToken = data
    },
    setError (state, error) {
      state.error = error
    },
    setRefreshError (state, error) {
      state.refreshError = error
    },
    setPopUpError (state, error) {
      state.popUpError = error
    },
    resetStates (state) {
      state.userData = null
      state.userToken = null
      state.error = null
      state.refreshError = null
      state.popUpError = null
    }
  },
  actions: {
    
  },
  modules: {
    login,
    logout,
    dashboard,
    applicants,
    applicant,
    questions,
    tests,
    users,
    account,
    settings
  }
})
