import jwtdecode from '../helpers/jwtdecode'

const url = process.env.VUE_APP_API_URL + "login"

export default {
  namespaced: true,

  state: {
    error: null,
    errors: [],
    isLoading: false
  },
  mutations: {
    setError (state, error) {
      state.error = error
    },
    setErrors (state, errors) {
      state.errors = errors
    },
    setIsLoading (state, val) {
      state.isLoading = val
    }
  },

  actions: {
    async sendLoginRequest({ commit }, data) {
      commit("setIsLoading", true)

      try {
        const response = await fetch(url, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(data)
        });

        const res = await response.json()
        
        if (!response.ok) {
          throw { response, res }
        } else {
          commit("setUserData", jwtdecode(res.userData), { root: true })
          commit("setUserToken", res.userData, { root: true })
          localStorage.setItem("userToken", res.userData)

          commit("setError", null) 
          commit("setErrors", []) 
        }
      } catch(error) {
        const errorStatus = error.response.status
        
        if (errorStatus==422) {
          commit("setError", null) 
          commit("setErrors", error.res.messages)
        } else {
          commit("setError", error.res.messages.error)
          commit("setErrors", []) 
        }
      } finally {
        commit("setIsLoading", false)
      }
    }
  }
}