<template>
  <v-container fluid class="px-5">
    <v-row class="mb-3" justify="space-between">
      <v-col cols="12" sm="6" md="4" xl="2" align-self="end">
        <v-tooltip top>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              icon
              color="blue darken-3 white--text"
              @click="showAddModal"
              v-bind="attrs"
              v-on="on"
              large
              class="mr-1"
            >
              <v-icon>mdi-plus</v-icon>
            </v-btn>
          </template>
          <span>Add Applicant</span>
        </v-tooltip>
        <v-tooltip top>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              icon
              color="secondary"
              @click="handleFilter"
              v-bind="attrs"
              v-on="on"
              class="mr-1"
            >
              <v-icon>mdi-cached</v-icon>
            </v-btn>
          </template>
          <span>Refresh Applicants</span>
        </v-tooltip>
        <downloadexcel
          class="d-inline-block"
          :data="recordsFormatted.reverse()" 
          :fields="{'Status': 'status', 'Name': 'name', 'Email': 'email', 'Contact': 'contact', 'Sales Group': 'sales_group', 'Position': 'position', 'Created At': 'formattedCreatedAt'}"
          :name="exportExcelName()"
        >
          <v-tooltip top>
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                icon
                color="secondary"
                v-bind="attrs"
                v-on="on"
              >
                <v-icon>mdi-file-export-outline</v-icon>
              </v-btn>
            </template>
            <span>Export to EXCEL</span>
          </v-tooltip>
        </downloadexcel>
      </v-col>
      <v-col cols="12" sm="6" md="4" xl="3">
        <v-text-field
          v-model="search"
          prepend-inner-icon="mdi-magnify"      
          label="Search"
          single-line
          hide-details="auto"
          :disabled="isLoading"
          clearable
        >
          <template v-slot:append>
            <v-tooltip
              top
            >
              <template v-slot:activator="{ on }">
                <v-btn
                  icon
                  color="secondary"
                  @click="clickSearchOptions"
                  v-on="on"
                >
                  <v-icon>mdi-menu-open</v-icon>
                </v-btn>
              </template>
              Search options
            </v-tooltip>
          </template>
        </v-text-field>
      </v-col>
    </v-row>
    <v-data-table
      :headers="headers"
      :items="recordsFormatted"
      item-key="id"
      class="elevation-1"
      :search="search"
      :loading="isLoading"
      loading-text="Loading... Please wait"
      :sort-by.sync="sortBy"
      :sort-desc.sync="sortDesc"
    >
      <template v-slot:item.actions="{ item }">
        <v-tooltip right>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              v-bind="attrs"
              v-on="on"
              @click="view({ id: item.id, name: item.name, email: item.email }, 0)"
              icon
              small
            >
              <v-icon
                size="large"
                color="blue darken-3"
              >
                mdi-newspaper-variant-outline
              </v-icon>
            </v-btn>
          </template>
          <span>View Applicant Info</span>
        </v-tooltip>

        <v-tooltip right>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              v-bind="attrs"
              v-on="on"
              @click="view({ id: item.id, name: item.name, email: item.email }, 1)"
              icon
              small
            >
              <v-icon
                size="large"
                color="blue darken-3"
              >
                mdi-note-edit-outline
              </v-icon>
            </v-btn>
          </template>
          <span>View Exam Info</span>
        </v-tooltip>

        <TableUpdateStatus :item="item" @updatedStatus="updatedStatus" />
        <TableSendApplicationLink :item="item" v-if="parseInt(item.status_id)==1" />
        <TableSendInterview :item="{ id: item.id }" v-if="parseInt(item.status_id)==2" />
      </template>
    </v-data-table>
    <ViewModal v-if="viewModal" :modalData="viewModalData" @closeModal="viewModal = false" @updatedStatus="updatedStatus" @updateData="updateData" />
    <AddModal v-if="addModal" :modalData="addModalData" @closeModal="addModal = false" @refresh="handleFilter" />
    <SearchOptionsModal v-if="searchOptionsModal" :modalData="searchOptionsModalData" @closeModal="searchOptionsModal = false" @searchOptionsSubmit="handleSearchOptions" />
  </v-container>
</template>

<script>
import { mapState, mapActions } from "vuex"
import { format, sub } from 'date-fns'
import downloadexcel from "vue-json-excel"
import AddModal from "@/components/applicants/AddModal"
import ViewModal from "@/components/applicants/ViewModal"
import TableSendApplicationLink from "@/components/applicants/TableSendApplicationLink"
import TableUpdateStatus from "@/components/applicants/TableUpdateStatus"
import SearchOptionsModal from "@/components/applicants/SearchOptionsModal"
import TableSendInterview from "@/components/applicants/TableSendInterview"

export default {
  components: { AddModal, ViewModal, TableSendApplicationLink, TableUpdateStatus, SearchOptionsModal, downloadexcel, TableSendInterview },
  data: () => ({
    search: '',
    sortBy: 'created_at',
    sortDesc: true,
    headers: [
      { 
        text: 'Actions', 
        value: 'actions', 
        sortable: false,
        align: 'center',
        width: 145,
      },
      { text: 'Status', value: 'status', width: 150 },
      { text: 'Name', value: 'name', width: 350 },
      { text: 'Email', value: 'email', width: 250 },
      { text: 'Contact', value: 'contact', width: 120 },
      { text: 'Sales Group', value: 'sales_group', width: 120 },
      { text: 'Position', value: 'position', width: 200 },
      { text: 'Created At', value: 'formattedCreatedAt', width: 180 },
    ],
    viewModal: false,
    viewModalData: [],
    addModal: false,
    addModalData: [],

    // Search Options
    searchOptionsModal: false,
    searchOptionsModalData: [],
    soStatus: '',
    soCreatedAt: [format(sub(new Date(), { days: 7 }), 'yyyy-MM-dd'), format(new Date(), 'yyyy-MM-dd')],
    soDateOfApplication: [],
    soStatusUpdatedAt: [],
    soSalesGroup: '',
    soPosition: ''
  }),
  created() {
    this.handleFilter()
  },
  /* watch: {
    // call again the method if the route changes
    '$route' (to) {
      if (isObjectEmpty(to.query)) {
        this.dates = []
      }

      this.handleFilter()
    }
  }, */
  computed: {
    ...mapState("applicants", ["records", "isLoading"]),
    recordsFormatted() {
      return this.records.map((record) => {
        return { ...record, formattedCreatedAt: this.formatDate(record.created_at, true) }
      })
    }
  },
  methods: {
    ...mapActions("applicants", ["getApplicants"]),
    formatDate(date, isTime = false) {
      if (date) {
        if (isTime) {
          return format(new Date(date), 'MMM dd, yyyy hh:mm a')
        } else {
          return format(new Date(date), 'MMM dd, yyyy')
        }
      } else {
        return '-'
      }
    },
    checkFilter() {
      let urlParams = new URLSearchParams(window.location.search) // ito gamit javascript and advantage nito kasi pwede sya kahit may param sa url, wala pa akong nakikita sa vue na may params at query string

      if (Object.keys(this.$route.query).length) {
        let status = urlParams.get('status'),
        createdAtStart = urlParams.get('created_at_start'),
        createdAtEnd = urlParams.get('created_at_end'),
        statusUpdatedAtStart = urlParams.get('status_updated_at_start'),
        statusUpdatedAtEnd = urlParams.get('status_updated_at_end')

        if (status) {
          this.soStatus = parseInt(status)
        }

        if (createdAtStart) {
          this.soCreatedAt = [createdAtStart, createdAtEnd]
        } else {
          this.soCreatedAt = []
        }

        if (statusUpdatedAtStart) {
          this.soStatusUpdatedAt = [statusUpdatedAtStart, statusUpdatedAtEnd]
        }
      }

      return true
    },
    handleSearchOptions(searchData) {
      this.searchOptionsModal = false // Remove Search Options Modal

      // Set values from Search Options
      this.soStatus = searchData.status
      this.soCreatedAt = searchData.created_at
      this.soDateOfApplication = searchData.date_of_application
      this.soStatusUpdatedAt = searchData.status_updated_at
      this.soSalesGroup = searchData.sales_group
      this.soPosition = searchData.position

      this.handleFilter()
    },
    handleFilter() { // fetchData dapat
      if (!this.isLoading) {
        if (this.checkFilter()) {
          this.$store.commit("applicants/setRecords", [])

          const searchOptionsData = {
            status: this.soStatus,
            createdAt: this.soCreatedAt,
            dateOfApplication: this.soDateOfApplication,
            statusUpdatedAt: this.soStatusUpdatedAt,
            salesGroup: this.soSalesGroup,
            position: this.soPosition,
          }

          this.getApplicants(searchOptionsData)
        }
      }
    },
    view({ id, name, email }, info) {
      this.viewModal = true
      this.viewModalData = { id, name, email, info, dialog: this.viewModal }
    },
    showAddModal() {
      this.addModal = true
      this.addModalData = { dialog: this.addModal }
    },
    clickSearchOptions() {
      this.searchOptionsModal = true
      this.searchOptionsModalData = { 
        dialog: this.searchOptionsModal,
        statusDefault: this.soStatus,
        createdAtDefault: this.soCreatedAt,
        dateOfApplicationDefault: this.soDateOfApplication,
        statusUpdatedAtDefault: this.soStatusUpdatedAt,
        salesGroupDefault: this.soSalesGroup,
        positionDefault: this.soPosition
      }
    },
    updatedStatus(id, status_id, status) {
      this.records.find((record) => record.id == id).status_id = status_id
      this.records.find((record) => record.id == id).status = status
    },
    exportExcelName() {
      return 'Applicants ' + format(new Date(), 'yyyy-MM-dd hh:mm:ss') + '.xls'
    },
    updateData(id, newData) {
      const recordTobeUpdate = this.records.find((record) => record.id == id)
      
      Object.entries(newData).forEach(([key, value]) => { 
        recordTobeUpdate[key] = value
      })
    }
    /* downloadCSVData() {
      let csv = 'Status,Name,Email,Contact,Sales Group,Position,Created At\n'
      let recordsReverse = this.recordsFormatted.reverse()
      recordsReverse.forEach(row => {
        csv += row.status + ","
        csv += row.name + ","
        csv += row.email + ","
        csv += "'" + row.contact + ","
        csv += row.sales_group + ","
        csv += row.position + ","
        csv += row.formattedCreatedAt + ","
        csv += "\n"
      })

      // console.log(csv)
  
      const anchor = document.createElement('a')
      anchor.href = 'data:text/csv;charset=utf-8,' + encodeURIComponent(csv)
      anchor.target = '_blank'
      anchor.download = 'applicants.csv'
      anchor.click()
    } */
  }
}
</script>