<template>
  <v-dialog
    v-model="dialog"
    scrollable
    width="800"
  >
    <v-card>
      <v-card-title>
        <span class="text-h6">{{ modalData.exam.name }}</span>
        <v-spacer></v-spacer>
        <v-btn
          icon
          @click="closeModal"
        >
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-card-title>
      <v-divider></v-divider>
      <v-card-text class="pt-5" style="max-height: 600px;">
        <div
          v-for="(item, index) in modalData.exam.questions"
          :key="index"
        >
          <p>{{ index + 1 }}. {{  item.question  }}</p>
          <div v-if="item.has_choices=='Yes'" class="mx-4 my-4">
            <v-row no-gutters>
              <v-col 
                cols="12" 
                md="6"
                v-for="(choice, index) in item.choices" 
                :key="index"
              >
                <v-alert
                  outlined
                  dense
                  :color="(choice.is_correct=='Correct') ? 'success' : 'secondary'"
                  class="mx-1 my-1"
                >
                  <v-icon v-if="choice.id==item.answer_id" class="mr-3" :color="(choice.is_correct=='Correct') ? 'success' : 'error'">mdi-checkbox-marked-circle</v-icon>{{ choice.description }}
                </v-alert>
              </v-col>
            </v-row>
          </div>
          <div v-else class="mx-4 my-4">
            <v-row no-gutters>
              <v-col 
                cols="12" 
              >
                <v-alert
                  outlined
                  dense
                  :color="(item.is_correct=='Correct') ? 'success' : 'error'"
                  class="mx-1 my-1"
                >
                  <small class="grey--text">Answered:</small> {{ item.raw_answer }}
                </v-alert>
              </v-col>
              <v-col 
                cols="12" 
                v-for="(choice, index) in item.choices" 
                :key="index"
              >
                <v-alert
                  outlined
                  dense
                  color="success"
                  class="mx-1 my-1"
                >
                  <small class="grey--text">Correct Answer:</small> {{ choice.description }}
                </v-alert>
              </v-col>
            </v-row>
          </div>
        </div>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  props: {
    modalData: {
      type: Object,
      required: true
    }
  },
  data: () => ({
    
  }),
  computed: {
    dialog: {
      get: function () {
        return this.modalData.dialog
      },
      // setter
      set: function () {
        this.closeModal()
      }
    },
  },
  methods: {
    closeModal() { 
      this.$emit('closeModal')
    }
  },
}
</script>