const url = process.env.VUE_APP_API_URL + "questions/"

export default {
  namespaced: true,

  state: {
    records: [],
    isLoading: false,
    addLoading: false,
    addError: null,
    updateLoading: false,
    updateError: null
  },

  mutations: {
    setRecords (state, data) {
      state.records = data
    },
    setIsLoading (state, val) {
      state.isLoading = val
    },
    setAddLoading (state, val) {
      state.addLoading = val
    },
    setAddError (state, error) {
      state.addError = error
    },
    setUpdateLoading (state, val) {
      state.updateLoading = val
    },
    setUpdateError (state, error) {
      state.updateError = error
    }
  },

  actions: {
    async getQuestions({ commit, rootState }) {
      commit("setIsLoading", true)

      try {
        const response = await fetch(`${url}get`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${rootState.userToken}`,
          }
        });

        const res = await response.json()
        
        if (!response.ok) {
          throw { response, res }
        } else {
          commit("setRecords", res.records)
        }
      } catch(error) {
        const errorStatus = error.response.status
        
        if (errorStatus==401) {
          commit("setError", "Unauthorized", { root: true })
        } else {
          commit("setRefreshError", "Submitted invalid data", { root: true })
        }
      } finally {
        commit("setIsLoading", false)
      }
    },

    async addQuestion({ commit, rootState }, data) {
      commit("setAddLoading", true)

      try {
        const response = await fetch(`${url}add`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${rootState.userToken}`,
          },
          body: JSON.stringify(data)
        });

        const res = await response.json()
        
        if (!response.ok) {
          throw { response, res }
        } else {
          commit("setAddError", null) 
        }
      } catch(error) {
        const errorStatus = error.response.status
        
        if (errorStatus==424) {
          commit("setAddError", error.res.error)
        } else if (errorStatus==401) {
          commit("setError", "Unauthorized", { root: true }) // force logout
        } else {
          commit("setPopUpError", "Something went wrong, please try again!", { root: true })
        }
      } finally {
        commit("setAddLoading", false)
      }
    },

    async updateQuestion({ commit, rootState }, data) {
      commit("setUpdateLoading", true)

      try {
        const response = await fetch(`${url}update`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${rootState.userToken}`,
          },
          body: JSON.stringify(data)
        });

        const res = await response.json()
        
        if (!response.ok) {
          throw { response, res }
        } else {
          commit("setUpdateError", null) 
        }
      } catch(error) {
        const errorStatus = error.response.status
        
        if (errorStatus==424) {
          commit("setAddError", error.res.error)
        } else if (errorStatus==401) {
          commit("setError", "Unauthorized", { root: true }) // force logout
        } else {
          commit("setPopUpError", "Something went wrong, please try again!", { root: true })
        }
      } finally {
        commit("setUpdateLoading", false)
      }
    },
  }
}