const url = process.env.VUE_APP_API_URL + "dashboard"

export default {
  namespaced: true,

  state: {
    records: [],
    recordsLoading: false,
  },

  mutations: {
    setRecords (state, data) {
      state.records = data
    },
    setRecordsLoading (state, val) {
      state.recordsLoading = val
    }, 
  },

  actions: {
    async getRecords({ commit, rootState }) {
      commit("setRecordsLoading", true)

      try {
        const response = await fetch(`${url}`, {
          method: 'GET',
          headers: {
            'Authorization': `Bearer ${rootState.userToken}`,
          }
        });

        const res = await response.json()
        
        if (!response.ok) {
          throw { response, res }
        } else {
          commit("setRecords", res)
        }
      } catch(error) {
        const errorStatus = error.response.status
        
        if (errorStatus==401) {
          commit("setError", "Unauthorized", { root: true })
        } else {
          commit("setRefreshError", "Submitted invalid data", { root: true })
        }
      } finally {
        commit("setRecordsLoading", false)
      }
    },
  }
}